<template>
  <component :is="loadingType"></component>
</template>
<script type="text/babel">
/**
 * w-loadingType by shang 2017/11/12
 * @desc Loading加载动画
 * @param {type} [str] - 动画类型
 * @param {size}  [str] - 尺寸
 *
 * @example
 * <w-loadingType type='light'></w-loadingType>
 * 
 * <w-loadingType type='1'></w-loadingType>
 *
 * <w-loadingType size="60"></w-loadingType>
 *
 */

  const LOADINGTYPE = [
    'light',
    'flower',
    'peas'
  ]
  const parseLoadingType = function(index) {
    // 如果传入的是数字 且 小于 动画类型的索引值 返回对应的动画 否则 默认第一个动画
    if ({}.toString.call(index) === '[object Number]') {
      if (LOADINGTYPE.length <= index) {
        console.warn(`'${index}' loadingType not found, use the default light.`);
        index = 0;
      }
      return LOADINGTYPE[index];
    }
    // 如果传入的名称 无法在 动画类型数组中找到 则默认  第一个动画
    if (LOADINGTYPE.indexOf(index) === -1) {
      console.warn(`'${index}' loadingType not found, use the default light.`);
      index = LOADINGTYPE[0];
    }
    return index;
  }

  import Light from './src/Light.vue'
  import Flower from './src/Flower.vue'
  import Peas from './src/Peas.vue'

  export default {
    name: 'w-loadingType',

    props: {
      type: {
        default: 0
      },
      size: {
        type: Number,
        default: 60
      }
    },

    computed: {
      loadingType() {
        return parseLoadingType(this.type);
      }
    },
    components: {
      'light': Light,
      'flower': Flower,
      'peas': Peas
    }
  }
</script>





