<template>
	<div>
		<div>不设置选中值,默认选中第一个</div>
		<w-picker :data="pickerData0" v-model="pickerValue0" :rowCount='5' ref="picker0"></w-picker>
		<w-cell title="选中值所对应的文字:">{{$refs.picker0 && $refs.picker0.getNameValues()}}</w-cell>
		<w-button type='primary' @click="setPickerValue('pickerValue0',['韩国'])">设置韩国</w-button>
		<div>多列,非联动</div>
		<w-picker :data="pickerData1" v-model="pickerValue1" :rowCount='5'></w-picker>
		<w-cell title="选中值所对应的文字:">{{pickerValue1}}</w-cell>
		<div>联动模式</div>
		<w-picker :data="pickerData3" v-model="pickerValue" :rowCount='5' ref="picker1" @on-change='onChange' @on-value-change='onValueChange' :columnWidthFlex="[2,1]" :linkage="true"></w-picker>
		<w-cell title="选中值所对应的文字:">{{$refs.picker1 && $refs.picker1.getNameValues()}}</w-cell>
		<w-cell title="选中值所对应的value:">{{pickerValue}}</w-cell>
		<w-button type='primary' @click="setPickerValue('pickerValue',['3','23'])">设置河北省,廊坊</w-button>
		<w-button type='primary' @click="comfirm">确定选择</w-button>
	</div>
</template>

<script>

export default {
	name: 'PickerDemo',
	data () {
		return {
			pickerData0: ['中国', '美国', '英国', '日本', '韩国', '意大利', '法国', '德国'],
			pickerValue0: [],
			pickerData1: [
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国'],
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国'],
				['中国', '美国', '英国', '日本', '韩国', '意大利', '法国']
			],
			pickerValue1: [],
			pickerData3: [
				[{
					name: '北京',
					value: '1'
				},
				{
					name: '天津',
					value: '2'
				},
				{
					name: '河北省',
					value: '3'
				},
				{
					name: '山东省',
					value: '4'
				},
				{
					name: '河南省',
					value: '5'
				},
				{
					name: '山西省',
					value: '6'
				},
				{
					name: '江苏省',
					value: '7'
				}],
				[{
					name: '北京市',
					value: '11',
					parent: '1'
				},
				{
					name: '天津市',
					value: '12',
					parent: '2'
				},
				{
					name: '石家庄',
					value: '13',
					parent: '3'
				},
				{
					name: '廊坊',
					value: '23',
					parent: '3'
				},
				{
					name: '德州',
					value: '14',
					parent: '4'
				},
				{
					name: '青岛',
					value: '24',
					parent: '4'
				},
				{
					name: '郑州',
					value: '15',
					parent: '5'
				},
				{
					name: '洛阳',
					value: '25',
					parent: '5'
				},
				{
					name: '太原',
					value: '16',
					parent: '6'
				},
				{
					name: '南京',
					value: '17',
					parent: '7'
				}
				]
			],
			pickerValue: [],
			pickerName: []
		}
	},
	created () {
	},
	mounted () {
		this.pickerName = this.$refs.picker1.getNameValues()
	},
	watch: {
		// 这种方法 获取的pickerName不是渲染后的数据,所以不正确 暂时放弃 还是使用{{$refs.picker1 && $refs.picker1.getNameValues()}} 这种双向绑定的方法
		// pickerValue () {
		// 	console.log('pickervalue change')
		// 	this.pickerName = this.$refs.picker1.getNameValues()
		// }
	},
	methods: {
		setPickerValue (oldPickerValue, pickerValue) {
			this[oldPickerValue] = pickerValue
		},
		onChange (val) {
			console.log('手动触发change:')
			console.log(val)
		},
		onValueChange (val) {
			console.log(`value发生变化[${val}]`)
		},
		comfirm () {
			let valueName = this.$refs.picker1.getNameValues()
			this.$WToast({
				message: `您选择的城市是${valueName},value是${this.pickerValue}`,
				maskShow: false,
				type: 'text',
				width: 300
			})
		}
	}
}
</script>

<style>

</style>



