<template>
	<button class="wind-btn" :class="classes" :disabled="disabled" :type="actionType" @click='handleClick'>
		<i class="wind-btn-loading-icon" v-if="showLoading"></i>
		<slot>{{ text }}</slot>
	</button>
</template>

<script type="text/babel">
/**
 * Button by shang 2017/12/30
 * @desc Button按钮
 * @param {type} [str] - 按钮样式类型 默认default 可选[default,primary,warn]
 * @param {disabled}  [boolean] - 禁用
 * @param {mini}  [boolean] - 小尺寸
 * @param {plain}  [boolean] - 镂空按钮
 * @param {text}  [str] - 按钮文本
 * @param {actionType}  [str] - 按钮类型 可选[button submit reset]
 * @param {showLoading}  [boolean] - 显示loading图标
 * @param {link}  [String] - 跳转的链接
 *
 * @event {click} 点击触发
 * 
 * @example
 *  <w-button type='primary' :disabled="false" :plain="false" :mini="false" :showLoading="false" @click='buttonClick'>确定</w-button>
 *	<w-button type='warn' :disabled="false" :plain="true" :mini="true">取消</w-button>
 *
 */
import { go } from '../../libs/router'
export default {
	name: 'w-button',
	props: {
		type: {
			default: 'default'
		},
		disabled: Boolean,
		mini: Boolean,
		plain: Boolean,
		text: String,
		actionType: String,
		showLoading: Boolean,
		link: String,
	},
	methods: {
		handleClick (event) {
			if(!this.disabled && !this.showLoading) {
				this.$emit('click', event)
				this.link !== undefined && go(this.link, this.$router)
			}
		}
	},
	computed: {
		classes () {
			return [
				{
					'wind-btn-disabled': this.disabled,
					'wind-btn-mini': this.mini
				},
				this.plain ? `wind-btn-plain-${this.type}` : `wind-btn-${this.type}`,
				this.showLoading ? `wind-btn-loading` : ''
			]
		}
	}
}
</script>

<style>
@import '../../src/style/style.css';

button.wind-btn {
	height: 41px;
	width: 100%;
	appearance: none;
	border-radius: 4px;
	border: 0;
	box-sizing: border-box;
	color: inherit;
	display: block;
	font-size: 18px;
	outline: 0;
	overflow: hidden;
	position: relative;
	text-align: center;
}
button.wind-btn.wind-btn-mini {
	width:auto;
	display: inline-block;
  font-size: 14px;
  padding: 0 12px;
  height: 33px;
}
button.wind-btn.wind-btn-disabled {
	opacity: .6;
}
button.wind-btn:focus {
	outline: 0;
}
button.wind-btn::after {
	background-color: #000;
	content: " ";
	opacity: 0;
	position: absolute;
	top:0;
	bottom:0;
	left:0;
	right:0;
}
button.wind-btn:not(.wind-btn-disabled):active::after {
	opacity: .4;
}
button.wind-btn-default {
	background-color: var(--button-default-background-color);
	color:var(--button-default-color);
	box-shadow: var(--button-default-box-shadow);
}
button.wind-btn-plain-default {
	border: 1px solid var(--button-default-plain-color);
	background-color: transparent;
	box-shadow: none;
	color: var(--button-default-plain-color);
}
button.wind-btn-primary {
	background-color: var(--button-primary-background-color);
	color:var(--button-primary-color);
}
button.wind-btn-plain-primary {
	border: 1px solid var(--button-primary-background-color);
	background-color: transparent;
	color: var(--button-primary-background-color);
}
button.wind-btn-warn {
	background-color: var(--button-warn-background-color);
	color:var(--button-warn-color);
}
button.wind-btn-plain-warn {
	border: 1px solid var(--button-warn-background-color);
	background-color: transparent;
	color: var(--button-warn-background-color);
}
.wind-btn + .wind-btn {
	margin-top: 10px;
}
button.wind-btn.wind-btn-loading.wind-btn-primary, button.wind-btn.wind-btn-loading.wind-btn-warn{
	color: rgba(255, 255, 255, 0.6);
}
</style>



