<template>
  <transition :name="animateName">
    <div class="wind-loading" v-show="isShow">
      <div class="wind-load-mask" @touchmove.prevent>
          <div class="wind-load-box" :style="{ 'padding': text ? '35px' : '15px' }">
              <w-loadingType :size="60" :type="loadingType"></w-loadingType>
              <span class="wind-loading-text" v-show="text">{{ text }}</span>
          </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/babel">
/**
 * Loading by shang 2017/11/8
 * @desc Loading提示框
 * 
 * @param {text} [str] - 显示在下方的文本信息
 * @param {loadingType}  [str] - loading动画样式
 *
 * @event {on-show} 显示时触发
 * @event {on-hide} 隐藏时触发
 *
 * @plugin 开启 vue.$WLoading('text').then() 或 vue.$WLoading({}).then()
 * @plugin 关闭 vue.$WAlert.end().then()
 * 
 * @then(arg) 
 * arg 开启或关闭Loading组件的回调
 * 
 * @example
 * <w-loading v-model="show"></w-loading>
 * 或
 * this.$Wloading('加载中...')
 *
 * 改变loading动画
 * loadingType:'动画名称' (可选:light,flower,peas)
 * this.$WLoading({
 *   text:'加载中...',
 *   loadingType:'flower'
 * })
 */
  import LoadingType from '../loadingType/index.js'

  export default {
    name: 'w-loading',

    data() {
      return {
        isShow: false,
        hasAnimate: true,
      }
    },
    created () {
      if (this.value) {
        this.isShow = true
      }
    },
    props: {
      value: Boolean,
      text: String,
      loadingType: {
        type: String,
        default: 'light'
      }
    },
    components: {
      'w-loadingType': LoadingType
    },
    computed: {
      /*
       * 动画效果样式，没有返回空
       * @return {String} 样式
       */
      animateName() {
        return this.hasAnimate ? 'opacity-transition' : ''
      },
    },
    methods: {
      /*
       * 开启动画效果
       */
      opemAnimate() {
        this.hasAnimate = true
      },
      /*
       * 去除动画效果
       * @return {Promise} 返回promise
       */
      removeAnimate() {
        return new Promise((resolve) => {
          this.hasAnimate = false
          resolve()
        })
      },
      show() {
        this.isShow = true
      },
      hide() {
        this.isShow = false
      }
    },
    watch: {
      value (val) {
        this.isShow = val
      },
      isShow (val) {
        this.$emit(val ? 'on-show' : 'on-hide')
      }
    }
  }
</script>

<style>

  .wind-loading {
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: transparent;
    transition: opacity .2s linear;
  }
 
  .wind-load-mask {
    position: fixed;
    z-index: 10000;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.4);
  }
  .wind-load-box {
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    border-radius:5px;
    background: rgb(0,0,0);
    padding: 5px
  }
  .wind-loading-text {
    position: absolute;
    width: 100%;
    left:0;
    bottom:0;
    display: block;
    color: #fff;
    text-align: center;
    font-size: 16px;
    white-space: nowrap;
    overflow: hidden;
  }
  /*渐进渐出动画*/
  .opacity-transition-enter, .opacity-transition-leave-active {
    opacity: 0;
  }
  .opacity-transition-leave {
    opacity: 1;
  }
</style>


