<template>
	<div class="wind-checklist">
		<w-cell :isLink="false"  v-for="(item, index) in currentOptions" :key="index" :prevent="false" :stopPropagation="false">
			<label class="wind-checklist-label" slot="cell-title">
				<span>
					<input type="checkbox" :value="item.value || item">
					<i class="icon-checked"></i>
				</span>
				<span v-text="item.name || item"></span>
			</label>
		</w-cell>
	</div>
</template>
<script>
/**
 * w-switch by shang 2017/11/1
 * @desc 切换按钮
 * @param {value} [boolean] - 绑定值，支持双向绑定
 * @param {slot}  [str] - 显示内容
 * @param {disabled} [slot] - 是否禁用
 *
 * @example
 * <w-switch v-model="value"></w-switch>
 * 1.一般使用绑定value即可实现双向绑定
 * 2.如有权限需求 绑定 disabled 即可,禁用状态下 按钮会有0.6的透明度
 * 3.需要根据开启/关闭 执行函数 绑定change 即可 v-on:change
 */
import Cell from '../cell/index.js'
export default {
	name: 'w-checkList',
	props: {
		value: Array,
		options: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	components: {
      'w-cell': Cell
    },
	data () {
		return {
			currentOptions: this.options,
			currentValue: this.value,
		}
	},
	// 实时计算
	computed: {
	}
}
</script>

<style lang="css">
.wind-checklist-label {
	width:100%;
	display: block;
}
.icon-checked {
	display: inline-block;
    vertical-align: middle;
    text-rendering: auto;
    border-radius: 100%;
    border: 1px solid #C9C9C9;
    font-size: 20px;
    background-color: white;
    width: 22px;
    height: 22px;
}
.icon-checked:before{
    border: 1px solid transparent;
	/*border-left: 0;*/
	/*border-top: 0;*/
	transform: rotate(45deg) scale(0);
	transition: transform .2s;
	position: relative;
    left: -1px;
}
</style>



