import Loading from './Loading.vue'

export default {
  /**
   * 每个插件都有的install方法，用于安装插件
   * @param {Object} Vue - Vue类
   * @param {Object} [pluginOptions] - 插件安装配置
   */
  install(Vue, pluginOptions = {}) {
    // 创建"子类"方便挂载
    const VueLoading = Vue.extend(Loading)
    let loading = null

    /**
     * 初始化并显示loading
     * @returns {Promise} Promise实例
     */
    function $Loading(options = {}) {
      return new Promise((resolve) => {
        // 第一次调用
        if (!loading) {
          loading = new VueLoading()
          // 手动创建一个未挂载的实例
          loading.$mount()
        }
        if (loading.isShow) {
          return
        }
        // loading()内可配置参数:text-显示loading文字 ,如果只传入字符串,则默认为text参数
        loading.text = typeof options === 'string' ? options : options.text || ''
        // loading()内可配置参数:loadingType-loading动画样式
        loading.loadingType = typeof options === 'object' ? options.loadingType : undefined
        // 挂载.
        document.querySelector(pluginOptions.container || 'body').appendChild(loading.$el)
        // 显示loading
        loading.show()
        resolve()
      })
    }
    // 定义关闭loading方法
    $Loading.end = (noAnimate = false) => {
      return new Promise((resolve) => {
        if (!loading || !loading.isShow) {
          resolve()
          return
        }
        // 判断是否在关闭时需要动画
        if (noAnimate) {
          // 默认只在此次行为下移除动画,之后的行为仍有动画
          loading.removeAnimate().then(() => {
            loading.opemAnimate()
          })
        }
        loading.hide()
        resolve()
      })
    }

    Vue.$WLoading = Vue.prototype.$WLoading = $Loading
  }
}


