<template>
  <transition :name="animateName">
    <div class="wind-dialog" v-show="isShow">
      <div class="wind-dialog-mask" @touchmove.prevent @click.self='hide'>
          <div class="wind-dialog-box" :style="{
            'width': typeof width === 'number'? `${width}px` : width,
            'height': typeof height === 'number'? `${height}px` : height
          }">
              <slot></slot>
          </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/babel">
/**
 * WDialog by shang 2017/11/8
 * @desc Dialog显示框
 * @param {value} [boolean] - 显示/关闭  支持双向绑定
 * @param {clickMaskHide} [boolean] - 点击遮罩层关闭
 * @param {width} [String,Number] - 自定义宽度(如果用str需要加上px,或百分号)
 * @param {height} [String,Number] - 自定义高度(如果用str需要加上px,或百分号)
 *
 * @event {on-show} 显示时触发
 * @event {on-hide} 隐藏时触发
 * @event {on-mask} 点击遮罩隐藏时触发
 * 
 * @example
 * <w-dialog v-model="show"></w-dialog>
 */

  export default {
    name: 'w-dialog',

    data() {
      return {
        isShow: false,
        hasAnimate: true,
      }
    },
    created () {
      if (this.value) {
        this.isShow = true
      }
    },
    props: {
      value: Boolean,
      clickMaskHide: {
        type: Boolean,
        default: false
      },
      width: [String,Number],
      height: [String,Number]
    },
     computed: {
      animateName() {
        return this.hasAnimate ? 'opacity-transition' : ''
      },
    },
    methods: {
      hide() {
        if (this.clickMaskHide) {
          this.isShow = false
          this.$emit('input', false)
          this.$emit('on-mask')
        }
      }
    },
    watch: {
      value (val) {
        this.isShow = val
      },
      isShow (val) {
        this.$emit(val ? 'on-show' : 'on-hide')
      }
    }
  }
</script>

<style>

  .wind-dialog {
    position: fixed;
    z-index: 10000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: transparent;
    transition: opacity .2s linear;
  }
 
  .wind-dialog-mask {
    position: fixed;
    z-index: 10000;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.4);
  }
  .wind-dialog-box {
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    border-radius:3px;
    background: rgb(255,255,255);
    /*width:80%;*/
    /*max-width: 300px;*/
    text-align: center;
  }
  
  /*渐进渐出动画*/
  .opacity-transition-enter, .opacity-transition-leave-active {
    opacity: 0;
  }
  .opacity-transition-leave {
    opacity: 1;
  }
</style>


