import Toast from './Toast.vue';
export default {
  /**
   * 每个插件都有的install方法，用于安装插件
   * @param {Object} Vue - Vue类
   * @param {Object} [pluginOptions] - 插件安装配置
   */
  install(Vue, pluginOptions = {}) {
    // 创建toast子类
    const VueToast = Vue.extend(Toast)
    // let instance = null

    let toastPool = []
    // 只生成一个dom的方法
    // let getAnInstance = () => {
    //   if (instance) {
    //     return instance;
    //   }
    //   return new VueToast({
    //     el: document.createElement('div')
    //   });
    // };
    let getAnInstance = () => {
      // console.log('实例个数',toastPool.length)
      if (toastPool.length > 0) {
        let instance = toastPool[0]
        toastPool.splice(0, 1)
        return instance
      }
      return new VueToast({
        el: document.createElement('div')
      })
    }

    let returnAnInstance = instance => {
      if (instance) {
        toastPool.push(instance)
      }
    }

    let removeDom = event => {
      if (event.target.parentNode) {
        event.target.parentNode.removeChild(event.target)
      }
    }

    VueToast.prototype.close = function() {
      this.visible = false
      this.maskShow = false
      this.$el.addEventListener('transitionend', removeDom)
      this.closed = true
      returnAnInstance(this)
    };

    let $Toast = (options = {}) => {
      return new Promise((resolve) => {
        let duration = options.duration || 3000
        duration = duration > 10000 ? 10000 : duration

        let instance = getAnInstance()
        // instance = getAnInstance()
        instance.closed = false
        clearTimeout(instance.timer);
        instance.message = typeof options === 'string' ? options : options.message
        instance.position = options.position || 'default'
        instance.className = options.className || ''
        instance.iconClass = options.iconClass || ''
        instance.type = options.type || 'success'
        instance.width = options.width || 120
        instance.maskShow = options.maskShow

        document.body.appendChild(instance.$el)
        Vue.nextTick(function() {
          instance.visible = true
          instance.$el.removeEventListener('transitionend', removeDom)
          ~duration && (instance.timer = setTimeout(function() {
            if (instance.closed) return;
            instance.close()
            resolve()
          }, duration))
        })
        return instance
      })
    }

    Vue.$WToast = Vue.prototype.$WToast = $Toast
  }
}



