<template>
	<div>
		<div class="button-group-title">插件方式调用</div>
		<div class="button-group">
			<w-button type='primary' @click="showToast1">Top</w-button>
			<w-button type='primary' @click="showToast2">Bottom</w-button>
			<w-button type='primary' @click="showToast3">Middle</w-button>
		</div>
		<w-cell>
			<span slot="cell-title">
				Default且关闭触发回调:{{value8?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value8" @change="showToast4"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				基本调用:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				显示遮罩层:{{value1?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value1"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				type为text:{{value2?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				type为cancel:{{value3?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				type为warning:{{value4?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value4"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				显示时间为1秒:{{value5?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value5"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				长文本:{{value6?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value6"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				自定义图标:{{value7?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value7"></w-switch>
			</span>
		</w-cell>
		<w-toast v-model="value" message="成功" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value1" message="显示遮罩层" :mask-show='true' @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value2" message="仅文本" position="middle" type="text" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value3" message="type为cancel" position="middle" type="cancel" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value4" message="type为警告" position="middle" type="warning" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value5" message="显示时间为1秒" position="middle" type="success" :duration="1000" @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value6" message="文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示文本提示" position="middle" type="text" :width='300' @on-show='onShow' @on-hide='onHide'></w-toast>
		<w-toast v-model="value7" message="自定义图标" position="middle" iconClass="github-icon-wind" @on-show='onShow' @on-hide='onHide'></w-toast>
	</div>
</template>

<script>

export default {
	name: 'dialogDemo',
	data () {
		return {
			value: false,
			value1: false,
			value2: false,
			value3: false,
			value4: false,
			value5: false,
			value6: false,
			value7: false,
			value8: false

		}
	},
	created () {
	},
	methods: {
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		},
		showToast1 () {
			this.$WToast({
				message: 'top提示',
				position: 'top',
				type: 'text',
				duration: 1000,
				width: '200px'
			})
		},
		showToast2 () {
			this.$WToast({
				message: 'bottom提示',
				position: 'bottom',
				type: 'text',
				duration: 1000
			})
		},
		showToast3 () {
			this.$WToast({
				message: 'middle提示',
				position: 'middle',
				type: 'text',
				duration: 1000
			})
		},
		showToast4 () {
			if (!this.value8) {
				return false
			}
			this.$WToast({
				message: 'default提示',
				type: 'text',
				duration: 1000
			}).then(() => {
				this.value8 = false
			})
		}
	}
}
</script>

<style>
	.button-group {
		padding-left:10px;
		padding-right:10px;
		margin-top:10px;
		margin-bottom:10px;
	}
	.button-group-title {
		text-align: left;
		padding-left:10px;
		padding-right:10px;
	}
	.github-icon-wind {
		background: url('../assets/github-white.png') no-repeat center center;
	}
</style>



