<template>
	<div>
		<w-cell title="显示索引,循环播放">当前索引:{{swipe1Index}}</w-cell>
		<w-swipe :height="200" :defaultIndex="swipe1Index"  :showIndexDot="true" :loop="true" :prevent="true" :stopPropagation="true" @on-index-change="changeItem" @on-item-click="onItemClick">
			<w-swipe-item class="slide1">item 1</w-swipe-item>
			<w-swipe-item class="slide2">item 2</w-swipe-item>
			<w-swipe-item class="slide3">item 3</w-swipe-item>
			<!-- <w-swipe-item class="slide1">
				<img src="../assets/能天使0.jpg" alt="" class="swipe-item-img">
			</w-swipe-item>
			<w-swipe-item class="slide2">
				<img src="../assets/能天使1.jpg" alt="" class="swipe-item-img">
			</w-swipe-item>
			<w-swipe-item class="slide3">
				<img src="../assets/能天使2.jpg" alt="" class="swipe-item-img">
			</w-swipe-item> -->
		</w-swipe>
		<w-cell title="不显示索引,不循环播放">当前索引:{{swipe2Index}}</w-cell>
		<w-swipe :height="200" :defaultIndex="swipe2Index"  :showIndexDot="false" :loop="false" :prevent="false" :stopPropagation="true" @on-index-change="changeItem2" @on-item-click="onItemClick2">
			<w-swipe-item class="slide1">item 1</w-swipe-item>
			<w-swipe-item class="slide2">item 2</w-swipe-item>
			<w-swipe-item class="slide3">item 3</w-swipe-item>
		</w-swipe>
	</div>
</template>

<script>

export default {
	name: 'SwipeDemo',
	data () {
		return {
			swipe1Index: 0,
			swipe2Index: 1
		}
	},
	created () {
	},
	methods: {
		changeItem (newIndex, oldIndex) {
			console.log(newIndex, oldIndex)
			this.swipe1Index = newIndex
		},
		onItemClick (currentIndex) {
			console.log(currentIndex)
		},
		changeItem2 (newIndex, oldIndex) {
			console.log(newIndex, oldIndex)
			this.swipe2Index = newIndex
		},
		onItemClick2 (currentIndex) {
			console.log(currentIndex)
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.slide {
	background-color: black;
	height: 200px;
}
.slide1 {
	background-color: #0089dc;
	color: #fff;
	height: 200px;
}
.slide2 {
	background-color: #ffd705;
	color: #000;
	height: 300px;
}
.slide3 {
	background-color: #ff2d4b;
	color: #fff;
	height: 200px;
}
.swipe-item-img {
	width:100%;
	height:100%;
}
</style>



