<template>
	<div>
		<w-cell title="simple-tabbar" :is-link="true" link="/SimpleTabbarDemo"></w-cell>
		<w-tabbar :value="selectTab">
			<w-tabbar-item>
				<img slot="icon" src="../assets/home.png">
				<img slot="icon-active" src="../assets/home-active.png">
				<span slot="label">Home</span>
			</w-tabbar-item>
			<w-tabbar-item :showDot='true'>
				<img slot="icon" src="../assets/demo.png">
				<img slot="icon-active" src="../assets/demo-active.png">
				<span slot="label">Demos</span>
			</w-tabbar-item>
			<w-tabbar-item iconClass="earth-icon">
				<span slot="label">News</span>
			</w-tabbar-item>
			<w-tabbar-item iconClass="github-icon-wind">
				<span slot="label">User</span>
			</w-tabbar-item>
		</w-tabbar>
	</div>
</template>

<script>

export default {
	name: 'tabbarDemo',
	data () {
		return {
			selectTab: 0
		}
	},
	created () {
		this.$parent.homeTabbarShow = false
	},
	// beforeDestroy  () {
	// 	this.$parent.homeTabbarShow = true
	// },
	methods: {
	}
}
</script>

<style>
.github-icon-wind {
	background: url('../assets/GitHub.png') no-repeat center center;
	background-size: 90%;
}
.earth-icon {
	background: url('../assets/earth.png') no-repeat center center;
	background-size: 90%;
}
</style>



