<template>
  <transition :name="animateName">
    <div class="wind-msgbox-alert">
      <w-dialog v-model="isShow" :clickMaskHide='clickMaskHide' @on-mask='clickMaskHideHandle' width="80%">
        <!-- title -->
        <div class="wind-msgbox-header">
          <strong class="wind-msgbox-title" :style="{
            'color':titleColor
          }">{{title}}</strong>
        </div>
        <!-- content -->
        <div class="wind-msgbox-content" :style="{
            'color':messageColor
          }">
          <slot>
            <div v-html="message"></div>
          </slot>
        </div>
        <!-- button -->
        <div class="wind-msgbox-btns">
          <button class="wind-msgbox-btn wind-msgboxbtn-confirm" @click="hide('confirm')">{{btnConfirmText}}</button>
        </div>
      </w-dialog>
    </div>
  </transition>
</template>
<script type="text/babel">
/**
 * Alert by shang 2017/12/4
 * @desc alert提示框
 * @param {value} [str] - 是否显示,用 v-model 绑定
 * @param {title}  [str] - 提示标题
 * @param {message}  [str] - 提示内容
 * @param {btnConfirmText}  [str] - confirm按钮内容
 * @param {titleColor}  [str] - 标题字体颜色
 * @param {messageColor}  [str] - 内容字体颜色
 * @param {clickMaskHide}  [boolean] - 是否开启点击遮罩关闭
 *
 * @event {on-show} 显示时触发
 * @event {on-hide} 隐藏时触发
 * @event {on-confirm} 点击确定按钮关闭时触发
 * @event {on-mask} 点击遮罩时触发
 * 
 * @plugin 开启 vue.$WAlert({})
 * @plugin 关闭 vue.$WAlert.close()
 * 
 * @then(arg1) 
 * arg1 插件调用的时候,点击confirm按钮和遮罩 都会触发then()内的回调
 * 
 * @example
 * <w-alert v-model="show"></w-alert>
 * 或
 * this.$WAlert('这是一个提示')
 * 或
 * this.$WAlert({
 *   title: '你好',
 *   message: '这是一个提示'
 *   clickMaskHide: true
 * }).then(() => {
 *   console.log('alert回调')
 *   this.switchValue13 = false
 * })
 *
 */
  import WDialog from '../w-dialog/WDialog.vue'

  export default {
    name: 'w-alert',

    data() {
      return {
        isShow: false,
        hasAnimate: true,
        callback: null
      }
    },
    created () {
      if (this.value) {
        this.isShow = true
      }
    },
    props: {
      value: Boolean,
      title: {
        type: String,
        default: '提示'
      },
      btnConfirmText: {
        type: String,
        default: '确定'
      },
      message: String,
      titleColor: {
        type: String,
        default: '#000'
      },
      messageColor: {
        type: String,
        default: '#999'
      },
      clickMaskHide: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'w-dialog': WDialog
    },
    computed: {
      animateName() {
        return this.hasAnimate ? 'opacity-transition' : ''
      },
    },
    methods: {
      show() {
        this.isShow = true
        this.$emit('input', true)
        this.$emit('on-show')
      },
      hide(action) {
        this.isShow = false
        this.$emit('input', false)
        // this.$emit('on-hide')
        this.$emit(action === 'confirm' ? 'on-confirm' : 'on-cancel')
        let callback = this.callback
        if ({}.toString.call(callback) === '[object Function]') {
          callback(action)
        }
      },
      clickMaskHideHandle () {
        this.$emit('input', false)
        this.$emit('on-mask')
        let callback = this.callback
        if ({}.toString.call(callback) === '[object Function]') {
          callback('confirm')
        }
      }
    },
    watch: {
      value (val) {
        this.isShow = val
      },
      isShow (val) {
        this.$emit(val ? 'on-show' : 'on-hide')
      }
    }
  }
</script>

<style>
  .wind-msgbox-header {
    padding: 15px 0 0;
  }
  .wind-msgbox-title {
    font-weight: 400;
    font-size: 18px;
  }
  .wind-msgbox-content {
    padding: 10px 20px 15px;
    border-bottom: 1px solid #ddd;
    min-height: 36px;
    position: relative;
    font-size: 15px;
    line-height: 2;
    word-wrap: break-word;
    word-break: break-all;
  }
  .wind-msgbox-btns {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 40px;
    line-height: 40px;
    box-sizing: border-box;
    font-size: 16px;
  }
  .wind-msgbox-btn {
    line-height: 35px;
    display: block;
    background-color: #fff;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    margin: 0;
    border: 0;
  }
  .wind-msgboxbtn-cancel {
    width: 50%;
    border-right: 1px solid #ddd;
  }
  .wind-msgboxbtn-confirm {
    color: #26a2ff;
    width: 50%;
  }
  /*渐进渐出动画*/
  .opacity-transition-enter, .opacity-transition-leave-active {
    opacity: 0;
  }
  .opacity-transition-leave {
    opacity: 1;
  }
</style>


