<template>
	<div class="wind-popup" v-show="show">
		<div class="wind-popup-mask" @click.self="onMaskClick" v-show="show && showMask" @touchmove.prevent></div>
		<!-- <a class="wind-popup-mask" @click.self="onMaskClick" v-show="show && showMask" @touchmove.prevent href='javascript:void(0)'></a> -->
		<transition :name="`wind-popup-transition-${position}`">
			<div v-show='topMove' :style="styles" class="wind-popup-wrapper" :class="`wind-popup-show-${position}`">
				<slot></slot>
			</div>
		</transition>
	</div>
</template>
<script type="text/babel">
/**
 * Popup by shang 2017/12/24
 * @desc popup 提示框
 * @param {value} [boolean] - 显示/关闭  支持双向绑定
 * @param {showMask} [boolean] - 显示/关闭  遮罩
 * @param {clickMaskHide} [Boolean] - 点击遮罩关闭
 * @param {position} [string] - 定位 可选 top,bottom,left,right
 * @param {popupStyle} [Object] - 主体窗口样式
 * @param {height} [string,number] - 高度, position为 top,bottom,或空 时有效(如果用str需要加上px,或百分号)
 * @param {width} [string,number] - 宽度, position为 left,right,或空 时有效(如果用str需要加上px,或百分号)
 * @param {isTransparent} [Boolean] - 主体窗口透明
 * @param {maxHeight} [string] - 最大高度
 *
 * @event {on-show} 显示时触发
 * @event {on-hide} 隐藏时触发
 *
 * @example
 * <w-popup v-model="switchValue15" :clickMaskHide="true" position='top' :show-mask='true' width="50%" height="50%" maxHeight="50%">
 * <div>显示文本</div>
 * <div>显示文本</div>
 * </w-popup>
 */

export default {
	name: 'w-popup',
	props: {
		value:Boolean,
		showMask: {
			type: Boolean,
			default: true
		},
		clickMaskHide: {
			type: Boolean,
			default: true
		},
		position:{
			type: String,
			default: 'bottom'
		},
		popupStyle: Object,
		height: {
			type: [String, Number],
			default: 'auto'
		},
		width: {
			type: [String, Number],
			default: 'auto'
		},
		isTransparent:{
			type: Boolean,
			default: false
		},
		maxHeight: String
	},
	data () {
		return {
			show: false,
			topMove: false,
			overflowStyle: '',
			overflowStyleX: '',
			overflowStyleY: ''
		}
	},
	methods: {
		onMaskClick () {
			if (this.clickMaskHide) {
				// this.topMove = false
				// this.$emit('input', false)
				// setTimeout(() => {
				// 	this.show = false
				// }, 250)
				this.close()
			}
		},
		open () {
			this.$emit('on-show')
			this.topMove = true
			this.show = true
			if (this.showMask) {
				this.bodyCannotScroll(true)
			}
		},
		close () {
			this.topMove = false
			this.$emit('input', false)
			this.$emit('on-hide')
			setTimeout(() => {
				this.show = false
				if (this.showMask) {
					this.bodyCannotScroll(false)
				}
			}, 250)
		},
		bodyCannotScroll (openflag) {
			let vuebody = document.querySelector('body')
			if (openflag) {
				this.overflowStyleY = vuebody.style.overflowY || ''
				vuebody.style.overflowY='hidden'
			} else {
				vuebody.style.overflowY = this.overflowStyleY
			}
		}
	},
	watch: {
		value(val) {
			if (val) {
				this.open()
			} else {
				this.close()
			}
		}
	},
	computed: {
		styles () {
			const styles = {}
			if (!this.position || this.position === 'bottom' || this.position === 'top') {
				if (this.maxHeight) {
					styles['max-height'] = this.maxHeight
				}
				styles.height = typeof this.height === 'number' ? `${this.height}px` : this.height
			} else {
				styles.width = typeof this.width === 'number' ? `${this.width}px` : this.width
			}
			this.isTransparent && (styles['background'] = 'transparent')
			if (this.popupStyle) {
				for (let i in this.popupStyle) {
				  styles[i] = this.popupStyle[i]
				}
			}
			return styles
		}
  },
}
</script>

<style>
	.wind-popup {
		z-index: 1000;
	}
	.wind-popup-mask{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 100;
		background-color: rgba(0,0,0,.4);
	}
	.wind-popup-wrapper{
		position: fixed;
		background-color: #eee;
		box-shadow: 0px 5px 10px rgba(0,0,0,.1);
		transition: .3s ease-out;
		z-index: 100;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		overflow-y: scroll;
		max-height: 100%;
	}
	.wind-popup-wrapper.wind-popup-show-top {
		top:0;
		bottom:auto;
		left:0;
		right:auto;
		width: 100%;
		height: auto;
	}
	.wind-popup-wrapper.wind-popup-show-bottom {
		top:auto;
		bottom:0;
		left:0;
		right:auto;
		width: 100%;
		height: auto;
	}
	.wind-popup-wrapper.wind-popup-show-left {
		top:0;
		bottom:auto;
		left:0;
		right:auto;
		width: auto;
		height: 100%;
	}
	.wind-popup-wrapper.wind-popup-show-right {
		top:0;
		bottom:auto;
		left:auto;
		right:0;
		width: auto;
		height: 100%;
	}
	/*top动画*/
	.wind-popup-transition-top-enter, .wind-popup-transition-top-leave-active {
		transform: translateY(-100%);
	}
	/*bottom动画*/
	.wind-popup-transition-bottom-enter, .wind-popup-transition-bottom-leave-active {
		transform: translateY(100%);
	}
	/*left动画*/
	.wind-popup-transition-left-enter, .wind-popup-transition-left-leave-active {
		transform: translateX(-100%);
	}
	/*right动画*/
	.wind-popup-transition-right-enter, .wind-popup-transition-right-leave-active {
		transform: translateX(100%);
	}
</style>


