<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				基本调用:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件方式调用
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2" @change='showAlert2'></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件方式调用,3秒后关闭
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3" @change='showAlert3'></w-switch>
			</span>
		</w-cell>
		<w-alert v-model="value" 
			title="提示" 
			message="消息发送成功" 
			:clickMaskHide="true" 
			@on-confirm="onConfirm" 
			@on-mask="onMask" 
			@on-show='onShow' 
			@on-hide='onHide'
		></w-alert>
	</div>
</template>

<script>

export default {
	name: 'alertDemo',
	data () {
		return {
			title: '提示',
			message: '消息发送成功',
			value: false,
			value2: false,
			value3: false
		}
	},
	created () {
	},
	methods: {
		showAlert2 () {
			if (!this.value2) {
				return false
			}
			this.$WAlert({
				message: this.message,
				clickMaskHide: true,
				btnConfirmText: '关闭',
				titleColor: 'red',
				messageColor: 'green'
			}).then(() => {
				console.log('alert回调')
				this.value2 = !this.value2
			})
		},
		showAlert3 () {
			if (!this.value3) {
				return false
			}
			if (this.AlertTimer) {
				clearTimeout(this.AlertTimer)
			}
			this.$WAlert({
				message: '3秒后自动关闭'
			}).then(() => {
				console.log('alert回调')
				this.value3 = false
			})
			this.AlertTimer = setTimeout(() => {
				this.$WAlert.close()
				this.value3 = false
			}, 3000)
		},
		onConfirm () {
			console.log('confirm')
		},
		onMask () {
			console.log('mask')
		},
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		}
	}
}
</script>

<style>

</style>



