<template>
	<div class="home-content">
		<div class="wind-logo">
			<img src="../assets/wind-logo.png" alt="">
			<p class="wind-notice">v0.16.0</p>
			<div class="cell-group">
				<w-cell title="Wind-Ui">by ShangHuaWei</w-cell>
				<w-cell title="Live Demo" @click.native="liveDemoCellClick" :isLink="true" link="/demos">
					<img slot="cell-icon" class="cell-icon" src="../assets/demo.png"></img>
				</w-cell>
				<w-cell title="Github" value="star me" :isLink="true" link="https://github.com/xiahouwei/wind-ui">
					<img slot="cell-icon" class="cell-icon" src="../assets/GitHub.png"></img>
				</w-cell>
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'home',
	data () {
		return {
			msg: 'Welcome to Your Vue.js App'
		}
	},
	created () {
		this.$parent.leftOptions.showBackBtn = false
		this.$parent.homeTabbarShow = true
	},
	methods: {
		liveDemoCellClick () {
			this.$parent.headerTitle = 'demo'
			this.$parent.selectTab = 1
		}
	},
	computed: {
	}
}
</script>

<style>
.home-content {
	width:100%;
}
.wind-logo {
	margin-top: 15px;
}
.wind-notice {
	line-height: 40px;
	font-size: 18px;
}
.cell-group {
	margin-top: 25px;
}
.cell-icon {
	width: 80%;
	height: 80%;
}
</style>



