<template>
	<div class="wind-picker">
		<div class="wind-picker-items" :style="{
				height:`${itemHeight * currentRowCount}px`
			}">
			<picker-item-group v-for="(itemData, index) in currentData" :itemData="itemData" v-model="currentValue[index]"	:key='index' :childIndex='index' :rowCount='currentRowCount' :itemHeight='itemHeight' :flex = 'columnWidthFlex[index]' :prevent='prevent' :stopPropagation='stopPropagation' @on-change='onChange'></picker-item-group>
		</div>
	</div>
</template>
<script>
/**
 * w-picker by shang 2018/2/25
 * @desc 选择器
 * @param {value} [Array] - picker value (two way binding)
 * @param {data}  [Array] - picker data
 * @param {itemHeight} [Number] - picker-item custom height (default 34)
 * @param {rowCount} [Number] - picker-item custom count(Odd default 7)
 * @param {columnWidthFlex} [Array] - picker-item custom width flex(last item flex could ellipsis)
 *
 * @Events {on-change} arg(val) - picker-item change by user(emit current change value)
 * val[Array] val[0] picker-item-value val[1] picker-item-index
 * @Events {on-value-change} arg(val) - picker-vale change emit(emit newValue)
 * 
 * @Methods {getNameValues} return currentNameArr of picker data
 * 
 * @example
 * <w-picker :data="pickerData" v-model="pickerValue" :rowCount='5' ref="picker1" @on-change='onChange' @on-value-change='onValueChange'></w-picker>
 */
const ITEM_HEIGHT = 34
const ROW_COUNT = 7
import PickerItemGroup from './picker-item.vue'
export default {
	name: 'w-picker',
	props: {
		data: Array,
		value: Array,
		itemHeight:{
			type: Number,
        	default: ITEM_HEIGHT
		},
		rowCount: {
	    	type: Number,
        	default: ROW_COUNT
	    },
	    columnWidthFlex: {
	    	type: Array,
        	default: () => {
			  return []
			}
	    },
	    // 是否开启联动模式
	    linkage: {
			type: Boolean,
			default: false
		},
	    // 阻止浏览器默认行为,提高效率
		prevent: {
			type: Boolean,
			default: true
		},
		// touchstart阻止冒泡
		stopPropagation: {
			type: Boolean,
			default: true
		}
	},
	components: {
      PickerItemGroup: PickerItemGroup
    },
    data () {
	    return {
	      currentData: this.data,
	      currentCopyData: [...this.data],
	      currentValue: this.value,
	      currentRowCount: this.rowCount
	    }
	},
    created () {
    	if (this.currentRowCount % 2 === 0) {
    		this.currentRowCount = this.currentRowCount + 1
    	}
    	if ({}.toString.call(this.data[0]) !== '[object Array]') {
	    	let array = []
	    	array.push(this.data)
	    	this.currentData = array
		}
		this.currentData = this.dataFormatObj(this.currentData)
		if ({}.toString.call(this.value) !== '[object Array]') {
	    	this.currentValue = []
		}
		if (this.linkage) {
			let valueLength = this.currentValue.length
			this.currentCopyData = [...this.currentData]
			for (let i = 0; i<this.currentCopyData.length; i++) {
				let value = this.currentValue[i]
				if (value === undefined) {
					return false
				}
				this.linkDatafilter(value,i+1)
			}
		}
	},
	watch: {
		value (val) {
			if (JSON.stringify(val) !== JSON.stringify(this.currentValue)) {
				// console.log('picker change',val)
				if({}.toString.call(val) !== '[object Array]') {
			    	this.currentValue = []
				} else {
					this.currentValue = val
				}
				// this.$emit('on-value-change', val)
			}
		},
		data (data) {
			// console.log('change data')
			if({}.toString.call(data[0]) !== '[object Array]') {
		    	let array = []
		    	array.push(data)
		    	this.currentData = this.dataFormatObj(array)
			} else {
				this.currentData = this.dataFormatObj(data)
			}
			
		},
		currentData (data) {
			// console.log('change currentdata')
		},
		currentValue (val) {
			// console.log('change currentValue',val)
			this.$emit('on-value-change', val)
			if (this.linkage) {
				this.currentData = this.getColumns(val)
				// console.log(this.currentData)
			}
			
		}
	},
	methods: {
		dataFormatObj (currentData) {
	    	for (let i = 0; i<currentData.length; i++) {
				for (let j = 0; j<currentData[i].length; j++) {
					let data = currentData[i][j]
					let dataType = typeof data
					if(dataType === 'string') {
						let obj = {
							name: data,
							value: data
						}
						currentData[i][j] = obj
					}
				}
			}
			return currentData
	    },
	    getNameValues (val) {
    		let currentValue = val || this.currentValue
	    	let currentData = this.currentData
	    	let currentNameArr = []
	    	for (let i = 0; i < currentValue.length; i++) {
	    		if (currentData[i] === undefined) {
	    			currentNameArr.push('undefined')
	    			break
	    		}
	    		let currentIndex = currentData[i].findIndex((data) => {
	    			return data.value === currentValue[i]
	    		})
	    		if (~currentIndex) {
	    			currentNameArr.push(currentData[i][currentIndex].name)
	    		}
	    	}
	    	return currentNameArr
	    },
	    onChange (msg) {
	    	let currentValue = msg[0]
	    	let childIndex = msg[1]
	    	let currentData = this.currentData[childIndex]
	    	let currentIndex = currentData.findIndex((data) => {
	    		return data.value === currentValue
	    	})
	    	let val = currentData[currentIndex]
	    	this.$emit('on-change', [val,childIndex])
	    	if (this.linkage) {
	    		let linkData = this.currentCopyData[childIndex+1]
		    	if (linkData !== undefined) {
		    		this.linkDatafilter(currentValue,childIndex+1)
		    	}
	    	}
	    },
	    linkDatafilter (val,index) {
	    	let linkdata = this.getChildren(val,index)
	    	this.currentData[index] = linkdata
	    	this.value[index] = linkdata[0].value
	    	if (this.currentCopyData[index+1]) {
	    		let currentValue = linkdata[0].value
	    		this.linkDatafilter(currentValue,index+1)
	    	}
	    },
	    getColumns (val) {
	    	let datas = []
	    	let currentData = this.currentData
	    	let max = currentData.length
	    	for (let i = 0; i<max; i++) {
	    		if (i === 0) {
	    			datas.push(currentData[i])
	    		} else {
	    			if (!val[i]) {
	    				// 没有数据时，取得上一级的第一个
	    				if (typeof datas[i - 1][0] === 'undefined') {
				            break
						} else {
							const topValue = datas[i - 1][0].value
							datas.push(this.getChildren(topValue,i))
						}
	    			} else {
	    				datas.push(this.getChildren(val[i - 1],i))
	    			}
	    		}
	    	}
	    	return datas
	    },
	    getChildren (val,index) {
	    	let linkdata = []
	    	let data = this.currentCopyData[index]
	    	for (let i = 0; i<data.length; i++) {
	    		if(data[i].parent === val) {
	    			linkdata.push(data[i])
	    		}
	    	}
	    	return linkdata
	    }
	}
}
</script>

<style lang="css">
.wind-picker .wind-picker-items {
    position: relative;
    overflow: hidden;
    width: 100%;
    display: flex;
}
</style>



