// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// import WindUI from 'wind-ui'  // 引入wind-ui

import WindUI from './windactionUIDev/index.js'


Vue.use(WindUI) // 使用风行UI

import './assets/style/app.css';//引用样式

Vue.config.productionTip = false


new Vue({
	el: '#app',
	router,
	template: '<App/>',
	components: { App }
})



