<template>
	<div>
		<div class="button-group-title">普通样式</div>
		<div class="button-group">
			<w-button >default</w-button>
			<w-button type='primary'>primary</w-button>
			<w-button type='warn'>warn</w-button>
		</div>
		<div class="button-group-title">禁用样式</div>
		<div class="button-group">
			<w-button type='primary' :disabled="true">primary</w-button>
		</div>
		<div class="button-group-title">镂空样式</div>
		<div class="button-group">
			<w-button :plain="true">default</w-button>
			<w-button type='primary' :plain="true">primary</w-button>
			<w-button type='warn' :plain="true">warn</w-button>
		</div>
		<div class="button-group-title">mini样式</div>
		<div class="button-group">
			<w-button :mini="true">default</w-button>
			<w-button type='primary' :mini="true">primary</w-button>
			<w-button type='warn' :mini="true">warning</w-button>
			</br>
			<w-button type='primary' :mini="true" :plain="true">primary</w-button>
			<w-button type='warn' :mini="true" :plain="true">warning</w-button>
		</div>
		<div class="button-group-title">longding样式</div>
		<div class="button-group">
			<w-button :showLoading="true" @click='buttonClick'>default</w-button>
			<w-button type='primary' :showLoading="true">primary</w-button>
			<w-button type='warn' :showLoading="true">warn</w-button>
		</div>
		<div class="button-group-title">跳转按钮</div>
		<div class="button-group">
			<w-button type='primary' link='/demos'>跳转</w-button>
		</div>
	</div>
</template>

<script>

export default {
	name: 'actionSheetDemo',
	data () {
		return {
		}
	},
	created () {
	},
	methods: {
		buttonClick () {
			console.log('触发button点击')
		}
	}
}
</script>

<style>
	.button-group {
		padding-left:10px;
		padding-right:10px;
		margin-top:10px;
		margin-bottom:10px;
	}
	.button-group-title {
		text-align: left;
		padding-left:10px;
		padding-right:10px;
	}
</style>



