<template>
  <div class="wind-swipe-item" :style="{
    'line-height':currentLineHeight
  }">
	<slot></slot>
  </div>
</template>

<script type="text/babel">
/**
 * swipe-item by shang 2018/1/28
 * @desc swipe-item : loop image item
 * 
 */
export default {
  name: 'w-swipe-item',
  props: {
    lineHeight:Number
  },
  mounted() {
  	this.$parent.pages.push(this);
  },
  beforeDestroy() {
	let index = this.$parent.pages.indexOf(this)
    if (index > -1) {
      this.$parent.pages.splice(index, 1)
    }
  },
  computed: {
    currentLineHeight() {
      return typeof this.lineHeight === 'number' ? `${this.lineHeight}px` : typeof this.$parent.height === 'number' ? `${this.$parent.height}px` : ''
    },
  }
}
</script>




