<template>
    <div class="wind-cell" @click="onClick" :class="{
        'wind-cell-disabled':disabled
    }">
        <div class="wind-cell-left">
            <slot name="cell-icon"></slot>
        </div>
        <div class="wind-cell-wrapper">
            <slot name="cell-title">
                <span v-if="title" class="wind-cell-text">{{title}}</span>
                <span v-if="label" class="wind-cell-label" v-text="label"></span>
            </slot>
        </div>
        <div class="wind-cell-right" 
             :class="{
                'wind-cell-link-arrow':isLink
             }">
            <slot>
                <span v-show="value">{{value}}</span>
            </slot>
            <slot name="cell-value"></slot>
        </div>
    </div>
</template>

<script type="text/babel">
/**
 * Cell by shang 2018/1/14
 * @desc Cell 单元格
 * @param {title}  [String] - 标题文本
 * @param {label}  [String] - 标题备注信息
 * @param {value}  [String] - 右侧显示文字
 * @param {isLink}  [Boolean] - 显示右侧箭头
 * @param {link}  [String] - 跳转的链接
 * 
 * @slot {cell-title}  - 同 title, 会覆盖 title 属性
 * @slot {cell-icon}  - 左侧icon插槽
 * @slot {cell-value}  - 同 value, 不会覆盖 value 属性
 *
 * @example
 *<w-cell title="Live Demo" @click.native="liveDemoCellClick" link="/helloWorld">
    <img slot="cell-icon" class="cell-icon" src="../assets/demo.png"></img>
  </w-cell>
 */
import { go } from '../../libs/router'
export default {
	name: 'w-cell',
	props: {
		title: String,
        label: String,
        value: String,
        isLink: {
            type: Boolean,
            default: false
        },
        link: String,
        disabled: {
            type: Boolean,
            default: false
        },
        // 阻止浏览器默认行为,提高效率
        prevent: {
            type: Boolean,
            default: false
        },
        // touchstart阻止冒泡
        stopPropagation: {
            type: Boolean,
            default: false
        }
	},
	methods: {
		onClick (event) {
            if (this.prevent) {
                event.preventDefault()
            }
            if (this.stopPropagation) {
                event.stopPropagation()
            }
            !this.disabled && this.link !== undefined && go(this.link, this.$router)
		}
	}
}
</script>

<style>
@import '../../src/style/style.css';

.wind-cell {
    background-color: var(--cell-background-color);
    box-sizing: border-box;
    min-height: 48px;
    overflow: hidden;
    position: relative;
    border-top: 1px solid #D9D9D9;
    border-bottom: 1px solid #D9D9D9;
    font-size: 16px;
    padding: 0 15px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    align-items: center;
}
.wind-cell-disabled {
    opacity: 0.5;
}
.wind-cell+.wind-cell{
    border-top: none;
}
.wind-cell .wind-cell-wrapper {
    flex:1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align:left;
}
.wind-cell .wind-cell-wrapper .wind-cell-text {
    color: var(--cell-title-color);
}
.wind-cell .wind-cell-wrapper .wind-cell-label {
    color: var(--cell-label-color);
    display: block;
    font-size: 12px;
    margin-top: 6px;
}
.wind-cell .wind-cell-left {
    padding-right: 10px;
}
.wind-cell .wind-cell-right {
    text-align: right;
    color: var(--cell-right-color);
    padding-right: 15px;
    position: relative;
    max-width: 70%;
}
.wind-cell .wind-cell-link-arrow:after {
    content: " ";
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: var(--cell-arrow-color);
    border-style: solid;
    -webkit-transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    position: absolute;
    top: 50%;
    margin-top: -4px;
    right:3px;
}
</style>



