<template>
  <div class="wind-light-loader" :style="{
    'height': loadingTypeSize,
    'width': loadingTypeSize,
    'line-height': loadingTypeSize
  }"></div>
</template>
<script type="text/babel">
  import common from './common.vue'

  export default {
    name: 'light',

    mixins: [common]
    
  }
</script>

<style>

.wind-light-loader {
    /*width: 60px;*/
    /*height: 60px;*/
    /*line-height: 60px;*/
    margin: 0 auto;
    position: relative;
    box-sizing: border-box;
    text-align: center;
    z-index: 0;
    text-transform: uppercase;
        -o-text-transform: uppercase;
        -ms-text-transform: uppercase;
        -webkit-text-transform: uppercase;
        -moz-text-transform: uppercase;
}

.wind-light-loader:before,
.wind-light-loader:after {
    opacity: 0;
    box-sizing: border-box;
        -o-box-sizing: border-box;
        -ms-box-sizing: border-box;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
    content: "\0020";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 32px;
    border: 2px solid rgb(255,255,255);
    box-shadow: 0 0 16px rgb(255,255,255), inset 0 0 16px rgb(255,255,255);
        -o-box-shadow: 0 0 16px rgb(255,255,255), inset 0 0 16px rgb(255,255,255);
        -ms-box-shadow: 0 0 16px rgb(255,255,255), inset 0 0 16px rgb(255,255,255);
        -webkit-box-shadow: 0 0 16px rgb(255,255,255), inset 0 0 16px rgb(255,255,255);
        -moz-box-shadow: 0 0 16px rgb(255,255,255), inset 0 0 16px rgb(255,255,255);
}

.wind-light-loader:after {
    z-index: 1;
    animation: cssload-gogoloader 2.3s infinite 1.15s;
        -o-animation: cssload-gogoloader 2.3s infinite 1.15s;
        -ms-animation: cssload-gogoloader 2.3s infinite 1.15s;
        -webkit-animation: cssload-gogoloader 2.3s infinite 1.15s;
        -moz-animation: cssload-gogoloader 2.3s infinite 1.15s;
}

.wind-light-loader:before {
    z-index: 2;
    animation: cssload-gogoloader 2.3s infinite;
        -o-animation: cssload-gogoloader 2.3s infinite;
        -ms-animation: cssload-gogoloader 2.3s infinite;
        -webkit-animation: cssload-gogoloader 2.3s infinite;
        -moz-animation: cssload-gogoloader 2.3s infinite;
}



@keyframes cssload-gogoloader {
    0% {
        transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 0;
    }
}

@-o-keyframes cssload-gogoloader {
    0% {
        -o-transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -o-transform: scale(1);
        opacity: 0;
    }
}

@-ms-keyframes cssload-gogoloader {
    0% {
        -ms-transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -ms-transform: scale(1);
        opacity: 0;
    }
}

@-webkit-keyframes cssload-gogoloader {
    0% {
        -webkit-transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(1);
        opacity: 0;
    }
}

@-moz-keyframes cssload-gogoloader {
    0% {
        -moz-transform: scale(0);
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        -moz-transform: scale(1);
        opacity: 0;
    }
}

</style>


