<template>
	<div class="wind-swipe" @touchstart="swipeTouchStart" @touchmove="swipeTouchMove" @touchend="swipeTouchEnd" :style="{
		'height':currentHeight
	}">
		<div class="wind-swipe-items-wrap" ref="wrap">
			<slot></slot>
		</div>
		<div class="wind-swipe-indexdots" v-show="showIndexDot">
			<div class="wind-swipe-indexdot"
				v-for="(page, $index) in pages"
				:class="{ 'is-active': $index === index }"></div>
		</div>
	</div>
</template>

<script type="text/babel">
/**
 * swipe by shang 2018/1/28
 * @desc swipe : loop image
 * @param {height} [Number] - custom height (default 100%)自定义高度
 * @param {defaultIndex}  [Number] - default display of the image index 默认显示索引
 * @param {showIndexDot}  [Boolean] - Whether show indexdot 显示索引点
 * @param {loop}  [Boolean] - Whether loop 是否循环滚动
 * @param {speed}  [Number] - img animation time 滚动速度
 * @param {interval}  [Number] - img loop display interval 滚动间隔
 * @param {prevent}  [Boolean] - blocking browser default events 浏览器默认行为
 * @param {stopPropagation}  [Boolean] - stopPropagation 阻止冒泡
 * 
 * swipe-item
 * @param {lineHeight} [Number] - loop image item lineHeight (default father height value)
 * 子组件高度
 *
 * @event {on-index-change} arg[val, oldVal]  img change emit(emit newIndex, oldIndex)
 * @event {on-item-click} arg[currentIndex]  img-item click emit(emit currentIndex)
 * 
 */
import { once } from '../../libs/dom'
export default {
	name: 'w-index-swipe',
	props: {
		height:Number,
		defaultIndex: {
			type: Number,
			default: 0
		},
		// 显示索引点
		showIndexDot: {
			type: Boolean,
			default: true
		},
		// 循环显示轮播图
		loop: {
			type: Boolean,
			default: true
		},
		// 图片移动的速度
		speed: {
			type: Number,
			default: 300
		},
		// 图片循环间隔时间
		interval: {
			type: Number,
			default: 3000
		},
		// 阻止浏览器默认行为,提高效率
		prevent: {
			type: Boolean,
			default: false
		},
		// touchstart阻止冒泡
		stopPropagation: {
			type: Boolean,
			default: true
		}
	},
	data () {
		return {
			pages: [],
			index: 0,
			timer:null,
			dragState: {},
			// 滑动中标记
			touchFlag: false,
			// 移动标记
			animateFlag: false,
			touchStartTime: ''
		}
	},
	created() {
		/*
		dragState = {
			startLeft           // 开始的toucheX坐标
			startTop            // 开始的toucheY坐标(相对于整个页面viewport pageY)
			startTopAbsolute    // 绝对Y坐标(相对于文档顶部 clientY)
			pageWidth           // 一个页面宽度
			pageHeight          // 一个页面的高度
			currentLeft			// 移动的toucheX坐标
			currentTop    		// 移动的toucheY坐标
			currentTopAbsolute  // 移动的绝对Y坐标
			prevPage            // 上一个页面
			dragPage            // 当前页面
			nextPage            // 下一个页面
		}
		*/
		this.dragState = {}
	},
	mounted () {
		this.initPage()
		this.initTimer()
	},
	/*销毁组件*/
	destroyed() {
	  if (this.timer) {
		this.clearTimer()
	  }
	},
	computed: {
	  currentHeight() {
		return typeof this.height === 'number' ? `${this.height}px` : '100%'
	  },
	},
	methods: {
		initPage () {
			let children = this.$children
			let defaultIndex = Math.floor(this.defaultIndex)
			defaultIndex = (this.defaultIndex >= 0 && this.defaultIndex < children.length) ? this.defaultIndex : 0
			this.index = defaultIndex
			for (let i = 0; i < children.legnth; i++){
				children[i].$el.classList.remove('is-active')
			}
			if (children.length > 0) {
				children[this.index].$el.classList.add('is-active')
			}
		},
		initTimer() {
			if (typeof this.interval !== 'number') {
				this.interval = 3000
			}
			this.timer = setInterval(() => {
				if (!this.loop && (this.index >= this.pages.length - 1)) {
				  	return this.clearTimer()
				} else if (this.loop && this.pages.length === 1) {
					return this.clearTimer()
				}
				if (!this.touchFlag && !this.animateFlag) {
					this.next()
				}
			}, this.interval)
		},
		clearTimer() {
			clearInterval(this.timer)
			this.timer = null
		},
		translate(element,offset,speed,callback) {
			offset = typeof offset === 'string' ? offset : `${offset}px`
			if (speed) {
				this.animateFlag = true
				element.style.webkitTransition = ''
				element.style.webkitTransition = `-webkit-transform ${speed}ms linear`
				setTimeout(() => {
					element.style.webkitTransform = `translate3d(${offset}, 0, 0)`
				}, 50)
				var transitionEndCallbackDone = false
				var transitionEndCallback = () => {
		            if (transitionEndCallbackDone) {
		            	return false
		            }
		            transitionEndCallbackDone = true
		            this.animateFlag = false
		            if (callback) {
		            	callback.apply(this, arguments)
		            }
		        }
		        once(element, 'webkitTransitionEnd', transitionEndCallback)
		        // 低版本安卓浏览器无法监听webkitTransitionEnd
          		setTimeout(transitionEndCallback, speed + 100)
			} else {
				/*清除一下webkitTransition，不然dom不能立即复位到下面的translate3d*/
				element.style.webkitTransition = ''
				element.style.webkitTransform = `translate3d(${offset}, 0, 0)`
			}
		},
		swipeTouchStart (event) {
			if (this.prevent) {
				event.preventDefault()
			}
        	if (this.stopPropagation) {
        		event.stopPropagation()
        	}
			this.touchStartTime = new Date()
			if (this.animateFlag){
				return false
			}
			this.touchFlag = true
			let element = this.$el
			let dragState = this.dragState
			let touch = event.touches[0]
			let children = this.$children
			let prevPage = children[this.index - 1]
			let dragPage = children[this.index]
			let nextPage = children[this.index + 1]
			// 触发时间
			dragState.startTime = new Date()
			// touch开始的X坐标
			dragState.startLeft = touch.pageX
			// touch开始的Y坐标
			dragState.startTop = touch.pageY
			// 绝对Y坐标
			dragState.startTopAbsolute = touch.clientY
			// 容器宽
			dragState.pageWidth = element.offsetWidth
			// 容器高
			dragState.pageHeight = element.offsetHeight
			// 允许循环 则 连接轮播图
			if (this.loop && children.length > 1) {
				if (!prevPage) {
					prevPage = children[this.$children.length - 1]
				}
				if (!nextPage) {
					nextPage = children[0]
				}
			}
			dragState.prevPage = prevPage ? prevPage.$el : null
			dragState.dragPage = dragPage ? dragPage.$el : null
			dragState.nextPage = nextPage ? nextPage.$el : null
			if (dragState.prevPage) {
				dragState.prevPage.style.display = 'block'
			}

			if (dragState.nextPage) {
				dragState.nextPage.style.display = 'block'
			}
		},
		swipeTouchMove (event) {
			if (!this.touchFlag) {
				return false
			}
			this.clearTimer()
			let dragState = this.dragState
			let touch = event.touches[0]
			// 移动的toucheX坐标
			dragState.currentLeft = touch.pageX
			// 移动的toucheY坐标
			dragState.currentTop = touch.pageY
			// 移动的绝对Y坐标
			dragState.currentTopAbsolute = touch.clientY
			// 移动的坐标(判断左右)
			let offsetLeft = dragState.currentLeft - dragState.startLeft
			if (offsetLeft > dragState.pageWidth ) {
				offsetLeft = dragState.pageWidth
			} else if (offsetLeft < (0-dragState.pageWidth)) {
				offsetLeft = 0 - dragState.pageWidth
			}
			let offsetTop = dragState.currentTopAbsolute - dragState.startTopAbsolute
			// 移动的距离
			let distanceX = Math.abs(offsetLeft)
			let distanceY = Math.abs(offsetTop)
			// 方向
			let direction = offsetLeft < 0 ? 'left' : 'right'
			if (dragState.prevPage && direction === 'right') {
				this.translate(dragState.prevPage,offsetLeft - dragState.pageWidth)
				this.translate(dragState.dragPage,offsetLeft)
			}
			if (dragState.nextPage && direction === 'left') {
				this.translate(dragState.nextPage,dragState.pageWidth + offsetLeft)
				this.translate(dragState.dragPage,offsetLeft)
			}
		},
		swipeTouchEnd (event) {
			let touchTime = new Date() - this.touchStartTime
			if (touchTime < 300) {
				this.$emit('on-item-click', this.index)
			}
			if (!this.touchFlag) {
				return false
			}
			let dragState = this.dragState
			let touch = event.touches[0]
			let children = this.$children
			// 移动的坐标(判断左右)
			let offsetLeft = dragState.currentLeft - dragState.startLeft
			let offsetTop = dragState.currentTopAbsolute - dragState.startTopAbsolute
			// 方向
			let direction = offsetLeft < 0 ? 'left' : 'right'
			if (Math.abs(offsetLeft) > dragState.pageWidth / 2) {
				if (direction === 'right' && dragState.prevPage){
					this.index = (this.index - 1) < 0 ? children.length - 1 : this.index - 1
					this.translate(dragState.prevPage,'0',300)
					this.translate(dragState.dragPage,dragState.pageWidth,300)
				}
				if (direction === 'left' && dragState.nextPage){
					this.index = (this.index + 1)  >= children.length ? 0 : this.index + 1
					this.translate(dragState.dragPage,-dragState.pageWidth,300)
					this.translate(dragState.nextPage,'0',300)
				}
			} else {
				if (direction === 'right' && dragState.prevPage){
					this.translate(dragState.prevPage,-dragState.pageWidth,300)
					this.translate(dragState.dragPage,'0',300)
				}
				if (direction === 'left' && dragState.nextPage){
					this.translate(dragState.nextPage,dragState.pageWidth,300)
					this.translate(dragState.dragPage,'0',300)
				}
			}
			this.touchFlag = false
			if (!this.timer) {
				setTimeout(()=>{
					this.initTimer()
				},50)
			}
		},

		next() {
			this.doAnimate('next')
		},
		// prev() {
		// 	this.doAnimate('prev')
		// },
		doAnimate() {
			let element = this.$el
			let dragState = this.dragState
			let children = this.$children
			let prevPage = children[this.index - 1]
			let dragPage = children[this.index]
			let nextPage = children[this.index + 1]
			dragState.pageWidth = element.offsetWidth
			
			// 允许循环 则 连接轮播图
			if (this.loop && children.length > 1) {
				if (!prevPage) {
					prevPage = children[this.$children.length - 1]
				}
				if (!nextPage) {
					nextPage = children[0]
				}
			}
			dragState.prevPage = prevPage ? prevPage.$el : null
			dragState.dragPage = dragPage ? dragPage.$el : null
			dragState.nextPage = nextPage ? nextPage.$el : null
			if (dragState.prevPage) {
				dragState.prevPage.style.display = 'block'
				this.translate(dragState.prevPage,-dragState.pageWidth)
			}
			if (dragState.nextPage) {
				dragState.nextPage.style.display = 'block'
				this.translate(dragState.nextPage,dragState.pageWidth)
			}
			this.index = (this.index + 1)  >= children.length ? 0 : this.index + 1
			setTimeout(() => {
				this.translate(dragState.dragPage,-dragState.pageWidth,300)
				this.translate(dragState.nextPage,'0',300)
			}, 50)
		}
	},
	watch: {
	  index(newIndex,oldIndex) {
		this.$emit('on-index-change', newIndex, oldIndex)
	  }
	}
}
</script>

<style>

.wind-swipe {
	overflow: hidden;
	position: relative;
	color: #fff;
	font-size: 30px;
	text-align: center;
}
.wind-swipe .wind-swipe-items-wrap {
	position: relative;
	overflow: hidden;
	height: 100%;
}
.wind-swipe .wind-swipe-items-wrap > div {
	position: absolute;
	transform: translateX(-100%);
	width: 100%;
	height: 100%;
	display: none;
}
.wind-swipe .wind-swipe-items-wrap > div.is-active {
	display: block;
	/*显示active的item*/
	transform: none;
}
.wind-swipe .wind-swipe-indexdots {
	position: absolute;
	bottom: 10px;
	left: 50%;
	transform: translateX(-50%);
}
.wind-swipe .wind-swipe-indexdots .wind-swipe-indexdot {
	width: 8px;
	height:8px;
	display: inline-block;
	border-radius: 100%;
	background: #000;
	opacity: 0.2;
	margin: 0 3px;
}
.wind-swipe .wind-swipe-indexdots .wind-swipe-indexdot.is-active {
	background: #fff;
}
</style>



