<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				基本使用:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				基本使用:{{value2?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				禁用:{{value3?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3" :disabled='true'></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				禁用:{{value4?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value4" :disabled='true'></w-switch>
			</span>
		</w-cell>
	</div>
</template>

<script>

export default {
	name: 'alertDemo',
	data () {
		return {
			value: false,
			value2: true,
			value3: false,
			value4: true
		}
	},
	created () {
	},
	methods: {
	}
}
</script>

<style>

</style>



