<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				开启dialog:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-dialog v-model="value" v-on:on-hide="onHide" v-on:on-show="onShow" :clickMaskHide="true" @on-mask="onMask" :width="270">
			<div class="img-div">
				<img src="../assets/exia.jpg" alt="图片" style="max-width:100%;">
			</div>
			<div @click="value=false">
				<span>关闭</span>
			</div>
		</w-dialog>
	</div>
</template>

<script>

export default {
	name: 'dialogDemo',
	data () {
		return {
			value: false
		}
	},
	created () {
	},
	methods: {
		onMask () {
			console.log('mask')
		},
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		}
	}
}
</script>

<style>
	/*.img-div {
		width:100%;
		height:100%;
	}*/
	/*.img-div img {
		width:100%;
		height:100%;
	}*/
</style>



