<template>
	<div>
		<div class="header-group">
			<w-header :title="headerTitle">
				<span slot="header-right">更多</span>
			</w-header>
		</div>
		<div class="header-group">
			<w-header :title="headerTitle" :leftOptions="leftOptions">
			</w-header>
		</div>
		<div class="header-group">
			<w-header :title="headerTitle" :leftOptions="leftOptions2"></w-header>
		</div>
		<div class="header-group">
			<w-header :title="headerTitle" :leftOptions="leftOptions3"></w-header>
		</div>
		<div class="header-group">
			<w-header :title="headerTitle" :leftOptions="leftOptions2" :rightOptions="rightOptions" @on-click-more="clickMore">
				<span slot="header-left" class='icon-actionsheet header-left-icon' @click="clickHeaderLeftIcon"></span>
			</w-header>
		</div>
		<w-actionSheet v-model="actionSheetShow" :menus="menus"></w-actionsheet>
		<w-popup v-model="popupShow" :clickMaskHide="true" position='left' height="100%" width="40%">
			<w-cell title="用户信息"></w-cell>
			<w-cell title="设置"></w-cell>
			<w-cell title="帮助"></w-cell>
		</w-popup>
	</div>
</template>

<script>

export default {
	name: 'headerDemo',
	data () {
		return {
			headerTitle: '标题',
			leftOptions: {
				showBackBtn: true,
	        	preventGoBack: false,
	        	leftBackText: 'goback'
			},
			rightOptions: {
				showMore: true
			},
			leftOptions2: {
				showBackBtn: false,
	        	preventGoBack: false
			},
			leftOptions3: {
				showBackBtn: true,
	        	preventGoBack: true,
	        	leftBackText: '阻止后退'
			},
			actionSheetShow: false,
			menus: [
				{
					label: '拍照'
				},
				{
					label: '从相册选择'
				}
			],
			popupShow: false
		}
	},
	created () {
	},
	methods: {
		clickMore () {
			this.actionSheetShow = true
		},
		clickHeaderLeftIcon () {
			this.popupShow = true
			console.log('leftIcon')
		}
	}
}
</script>

<style>
	.header-group {
		position: relative;
	}
	.header-group:nth-child(1) {
		margin-top:20px;
	}
	.header-group:nth-child(2) {
		margin-top:80px;
	}
	.header-group:nth-child(3) {
		margin-top:140px;
	}
	.header-group:nth-child(4) {
		margin-top:200px;
	}
	.header-group:nth-child(5) {
		margin-top:260px;
	}
	.header-left-icon {
		font-size: 18px;
	}
</style>



