<template>
	<div class="demos">
		<div class="demos-row">
			<div class="demos-item" @click="itemClick('/actionSheetDemo')">
				<span class="icon-span icon-actionsheet"></span>
				<span class="text-span">ActionSheet</span>
			</div>
			<div class="demos-item" @click="itemClick('/alertDemo')">
				<span class="icon-span icon-alert"></span>
				<span class="text-span">Alert</span>
			</div>
			<div class="demos-item" @click="itemClick('/buttonDemo')">
				<span class="icon-span icon-button"></span>
				<span class="text-span">Button</span>
			</div>
		</div>
		<div class="demos-row">
			<div class="demos-item" @click="itemClick('/cellDemo')">
				<span class="icon-span icon-cell"></span>
				<span class="text-span">Cell</span>
			</div>
			<div class="demos-item" @click="itemClick('/dialogDemo')">
				<span class="icon-span icon-dialog"></span>
				<span class="text-span">Dialog</span>
			</div>
			<div class="demos-item" @click="itemClick('/confirmDemo')">
				<span class="icon-span icon-confrim"></span>
				<span class="text-span">Confirm</span>
			</div>
		</div>
		<div class="demos-row">
			<div class="demos-item" @click="itemClick('/headerDemo')">
				<span class="icon-span icon-header"></span>
				<span class="text-span">Header</span>
			</div>
			<div class="demos-item" @click="itemClick('/IndexListDemo')">
				<span class="icon-span icon-indexList"></span>
				<span class="text-span">IndexList</span>
			</div>
			<div class="demos-item" @click="itemClick('/loadingDemo')">
				<span class="icon-span icon-loading"></span>
				<span class="text-span">Loading</span>
			</div>
		</div>
		<div class="demos-row">
			<div class="demos-item" @click="itemClick('/pickerDemo')">
				<span class="icon-span icon-picker"></span>
				<span class="text-span">Picker</span>
			</div>
			<div class="demos-item" @click="itemClick('/popupDemo')">
				<span class="icon-span icon-popup"></span>
				<span class="text-span">Popup</span>
			</div>
			<div class="demos-item" @click="itemClick('/swipeDemo')">
				<span class="icon-span icon-swipe"></span>
				<span class="text-span">Swipe</span>
			</div>
		</div>
		<div class="demos-row">
			<div class="demos-item" @click="itemClick('/switchDemo')">
				<span class="icon-span icon-switch"></span>
				<span class="text-span">Switch</span>
			</div>
			<div class="demos-item" @click="itemClick('/tabbarDemo')">
				<span class="icon-span icon-tabbar"></span>
				<span class="text-span">Tabbar</span>
			</div>
			<div class="demos-item" @click="itemClick('/toastDemo')">
				<span class="icon-span icon-toast"></span>
				<span class="text-span">Toast</span>
			</div>
		</div>
		<div class="demos-row" v-if="false">
			<div class="demos-item" @click="itemClick('/checkListDemo')">
				<span class="icon-span icon-checkList"></span>
				<span class="text-span">CheckList</span>
			</div>
			<div class="demos-item">
			</div>
			<div class="demos-item">
			</div>
		</div>
	</div>
</template>

<script>

export default {
	name: 'demos',
	data () {
		return {
		}
	},
	beforeRouteLeave (to, from, next) {
		if (to.path === '/home') {
			this.$parent.headerTitle = 'home'
			this.$parent.selectTab = 0
		}
		next()
	},
	created () {
		this.$parent.leftOptions.showBackBtn = false
		this.$parent.homeTabbarShow = true
	},
	methods: {
		itemClick (url) {
			this.$router.push(url)
			this.$parent.leftOptions.showBackBtn = true
		}
	}
}
</script>

<style>
.demos-row {
	width:100%;
	height:100px;
	display: flex;
}
.demos-row >.demos-item {
	flex:1;
	border-bottom: 1px solid #c7c7c7;
	border-right: 1px solid #c7c7c7;
}
.demos-row .demos-item:nth-child(3) {
	border-right:none;
}
.icon-span, .text-span {
	display: block;
	width: 100%;
	text-align: center;
}
.demos-row .demos-item .icon-span {
	height: 60px;
	line-height: 60px;
	font-size: 36px;
}
.text-span {
	font-size: 16px;
	margin-top: 5px;
}
</style>



