import Confirm from './Confirm.vue'

export default {
  /**
   * 每个插件都有的install方法，用于安装插件
   * @param {Object} Vue - Vue类
   * @param {Object} [pluginOptions] - 插件安装配置
   */
  install(Vue, pluginOptions = {}) {
    // 创建"子类"方便挂载
    const VueConfirm = Vue.extend(Confirm)
    let confirm = null
    let confirmCallbackObj = {}
    const defaultCallback = action => {
      if (confirmCallbackObj.resolve) {
        if (action === 'confirm') {
          confirmCallbackObj.resolve(action)
        } else if (action === 'cancel' && confirmCallbackObj.reject) {
          confirmCallbackObj.reject(action)
        }
      }
    }

    /**
     * 初始化并显示confirm
     * @returns {Promise} Promise实例
     */
    function $confirm(options = {}) {
      return new Promise((resolve, reject) => {
        // 第一次调用
        if (!confirm) {
          confirm = new VueConfirm()
          // 手动创建一个未挂载的实例
          confirm.$mount()
        }
        // if (confirm.isShow) {
        //   return
        // }
        confirm.title = options.title || '提示'
        confirm.message = typeof options === 'string' ? options : options.message || ''
        confirm.btnConfirmText = options.btnConfirmText || '确定'
        confirm.btnCancelText = options.btnCancelText || '取消'
        confirm.titleColor = options.titleColor || '#000'
        confirm.messageColor = options.messageColor || '#999'
        confirm.clickMaskHide = options.clickMaskHide || false
        confirm.callback = defaultCallback
        if (typeof Promise !== 'undefined') {
          confirmCallbackObj.resolve = resolve
          confirmCallbackObj.reject = reject
        }
        // 挂载.
        document.querySelector(pluginOptions.container || 'body').appendChild(confirm.$el)
        // 显示confirm
        confirm.show()
        // resolve()
      })
    }
    // 定义关闭confirm方法
    $confirm.close = () => {
      return new Promise((resolve) => {
        if (!confirm || !confirm.isShow) {
          resolve()
          return
        }
        confirmCallbackObj = {}
        confirm.hide()
        resolve()
      })
    }

    Vue.$WConfirm = Vue.prototype.$WConfirm = $confirm
  }
}


