<template>
	<div class='wind-picker-item-group' @touchstart="pickerTouchStart" @touchmove="pickerTouchMove" @touchend="pickerTouchEnd" :style="{
			flex:flex
		}">
		<div class="wind-picker-mask" :style="{
				backgroundSize:`100% ${Math.floor(rowCount / 2) * itemHeight}px`
			}"></div>
		<div class="wind-picker-item-view" :style="{
				height:`${itemHeight}px`,
				top:`${Math.floor(rowCount / 2) * itemHeight}px`
			}"></div>
		<div class='wind-picker-item-content' :class="{ dragging: dragging }" :style="{
			transform:`translateY(${currentTranslateY}px)`
		}" ref="pickerItem">
			<div class='wind-picker-item' v-for="(item, index) in currentData" :style="{
				height:`${itemHeight}px`,
				lineHeight:`${itemHeight}px`
			}">
				{{item.name || item}}
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name: 'w-picker-item-group',
	props: {
		value: String,
		itemHeight: Number,
	    rowCount: Number,
		itemData: {
	        type: Array,
	        default() {
	          return [];
	        }
	    },
	    childIndex:Number,
	    flex: {
	    	type: Number,
	    	default: 1
	    },
	    // 阻止浏览器默认行为,提高效率
		prevent: {
			type: Boolean,
			default: true
		},
		// touchstart阻止冒泡
		stopPropagation: {
			type: Boolean,
			default: true
		}
	},
	data () {
		return {
			dragState: {},
			currentData: this.itemData,
			currentValue: this.value,
			currentIndex: 0,
			// touch标记
			touchFlag: false,
			// 拖拽
			dragging:false,
			// 移动中标记
			animateFlag: false,
			touchStartTime: '',
			// 控制item显示位置
			currentTranslateY: 0
		}
	},
	created() {
		/*
		dragState = {
			startLeft           // 开始的toucheX坐标
			startTop            // 开始的toucheY坐标(相对于整个页面viewport pageY)
			startTopAbsolute    // 绝对Y坐标(相对于文档顶部 clientY)
			pageWidth           // 一个页面宽度
			pageHeight          // 一个页面的高度
			currentLeft			// 移动的toucheX坐标
			currentTop    		// 移动的toucheY坐标
			currentTopAbsolute  // 移动的绝对Y坐标
			topTranslateY		// 滚动上限
			bottomTranslateY	// 滚动下线
			currentTranslateY   // 记录选项列表显示位置
			distanceY			// 移动的距离
		}
		*/
		this.dragState = {}
		let currentValue = this.currentValue
		this.translateValue(currentValue)
	},
	methods: {
		translateValue (currentValue) {
			// let currentData = this.currentData
			// 由于父级的watch:value 会优先触发 所以 这里的currentData还是父级未改变的data
			// 所以暂时使用绑定的itemData,找到解决办法再修改($nextTick也不好使)
			let currentData = this.itemData
			// console.log(currentData)
			let currentIndex = currentData.findIndex((data) => {
				return data.value === currentValue
			})
			if (!~currentIndex) {
				let value = currentData[0].value
				this.currentIndex = 0
				this.currentValue = value
				this.$emit('input', value)
			} else {
				this.currentIndex = currentIndex
			}
			this.currentTranslateY = this.getTranslateByIndex(this.currentIndex)
		},
		pickerTouchStart (event) {
			if (this.animateFlag){
				return false
			}
			if (this.prevent) {
				event.preventDefault()
			}
        	if (this.stopPropagation) {
        		event.stopPropagation()
        	}
			this.touchStartTime = new Date()
			this.touchFlag = true
			this.dragState = {}
			let dragState = this.dragState
			let touch = event.touches[0]
			let element = this.$el
			let elHeight = element.offsetHeight
			// 滚动上限
			dragState.topTranslateY = elHeight/2
			// 滚动下限
			dragState.bottomTranslateY = dragState.topTranslateY - this.$refs.pickerItem.offsetHeight
			// 触发时间
			dragState.startTime = new Date()
			// touch开始的X坐标
			dragState.startLeft = touch.pageX
			// touch开始的Y坐标
			dragState.startTop = touch.pageY
			// 绝对Y坐标
			dragState.startTopAbsolute = touch.clientY
			// 记录滑动位置
			dragState.currentTranslateY = this.currentTranslateY
		},
		pickerTouchMove (event) {
			this.dragging = true
			let dragState = this.dragState
			let touch = event.touches[0]
			// 移动的toucheX坐标
			dragState.currentLeft = touch.pageX
			// 移动的toucheY坐标
			dragState.currentTop = touch.pageY
			// 移动的绝对Y坐标
			dragState.currentTopAbsolute = touch.clientY
			// 移动的距离
			dragState.distanceY = dragState.currentTopAbsolute - dragState.startTopAbsolute
			let currentTranslateY = dragState.currentTop - dragState.startTop + dragState.currentTranslateY
			if (currentTranslateY > dragState.topTranslateY) {
				this.currentTranslateY = dragState.topTranslateY - 1
				return false
			}
			if (currentTranslateY < dragState.bottomTranslateY) {
				this.currentTranslateY = dragState.bottomTranslateY
				return false
			}
			this.currentTranslateY = currentTranslateY
		},
		pickerTouchEnd (event) {
			if (!this.touchFlag) {
				return false
			}
			if (!this.dragging) {
				return false
			}
			this.dragging = false

			let dragState = this.dragState
			let touch = event.touches[0]
			let itemHeight = this.itemHeight
			let currentData = this.currentData
			let currentTranslateY = this.currentTranslateY
			let touchTime = new Date() - this.touchStartTime
			let _translateY
			if (touchTime < 150) {
				// console.log('开启惯性')
				// console.log(touchTime)
				// console.log(dragState.distanceY)
				// 惯性速率
				let velocity =  1 / touchTime * 100
				// 惯性距离
				let momentumTranslate = currentTranslateY + dragState.distanceY * velocity
				if (momentumTranslate > dragState.topTranslateY) {
					momentumTranslate = dragState.topTranslateY - 1
				}
				if (momentumTranslate < dragState.bottomTranslateY) {
					momentumTranslate = dragState.bottomTranslateY
				}
				_translateY = Math.round(momentumTranslate / itemHeight) * itemHeight
			} else {
				_translateY = Math.round(currentTranslateY / itemHeight) * itemHeight
			}
			// console.log(_translateY)
			this.currentTranslateY = _translateY
			this.currentIndex = this.getIndexByTranslate(_translateY)
			// console.log(this.currentIndex)
			this.currentValue = currentData[this.currentIndex].value
			// console.log(this.currentValue)
			this.emitCurrentValue(this.currentValue)
		},
		// 根据translate计算index
		getIndexByTranslate (translate) {
			let itemHeight = this.itemHeight
	        let index = (Math.floor(this.rowCount / 2) * itemHeight -translate) / itemHeight
	        return index
		},
		// 根据index计算translate
		getTranslateByIndex (index) {
			let itemHeight = this.itemHeight
			let translate = (Math.floor(this.rowCount / 2) - index) * itemHeight
			return translate
		},
		emitCurrentValue (val) {
			this.$emit('input', val)
			this.$emit('on-change', [val,this.childIndex])
		}
	},
	watch: {
		value (val) {
			// console.log('picker-item change',val)
			this.currentValue = val
			this.translateValue(val)
		},
		itemData(data) {
			// console.log('change itemdata')
			this.currentData = data
		}
	}
}
</script>

<style lang="css">
.wind-picker-item-group {
	height: 100%;	
	/*flex: 1;*/
	position: relative;
}
.wind-picker-mask {
	height: 100%;
	width:100%;
	position: absolute;
	top: 0;
	left: 0;
	background-image: linear-gradient(to bottom, rgba(255,255,255,0.95), rgba(255,255,255,0.6)), linear-gradient(to top, rgba(255,255,255,0.95), rgba(255,255,255,0.6));
	background-position: top, bottom;
    background-repeat: no-repeat;
    z-index: 3;
}
.wind-picker-item-view {
	width: 100%;
    position: absolute;
    left: 0;
    z-index: 3;
    border-top:1px solid #eee;
    border-bottom:1px solid #eee;
}
.wind-picker-item-group .wind-picker-item-content {
	position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
    z-index: 1;
}
.wind-picker-item-group .wind-picker-item-content.dragging {
	 transition-duration: 0s;
}
.wind-picker-item-group .wind-picker-item-content .wind-picker-item {
	text-align: center;
    font-size: 16px;
    color: #000;
}
</style>



