<template>
  <div class="wind-tabbar">
    <slot></slot>
  </div>
</template>

<script type="text/babel">
/**
 * Tabbar by shang 2018/1/6
 * @desc 底部tabbar标签
 * @param {value} [Number] - 选中的子元素
 * @param {iconClass}  [String] - 图标样式
 * 
 * @slot {label} tabbar选项名称
 * 
 * @event {on-index-change} arg[val, oldVal]  点击触发(发送当前选中子元素索引,原子元素索引)
 *
 * Tabbar-item by shang 2018/1/6
 * @desc 底部tabbar-item 标签选项
 * @param {showDot} [boolean] - 待处理红点
 * @param {disabled}  [boolean] - 禁用
 * @param {iconClass}  [String] - 图标样式
 * @param {selected}  [boolean] - 选中
 *
 * @slot {icon}  tabbar选项图标
 * @slot {icon-active} tabbar选项图标选中状态
 *
 * @event {on-item-click} arg[currentIndex]  点击触发(发送当前标签索引)
 * 
 * @example
  <w-tabbar>
    <w-tabbar-item :selected='true' @on-item-click="itemClick">
      <img slot="icon" src="../assets/micOff.png">
      <img slot="icon-active" src="../assets/micOn.png">
      <span slot="label">News</span>
    </w-tabbar-item>
    <w-tabbar-item :showDot='true' @on-item-click="itemClick">
      <img slot="icon" src="../assets/micOff.png">
      <img slot="icon-active" src="../assets/micOn.png">
      <span slot="label">News</span>
    </w-tabbar-item>
  </w-tabbar>
 * 
 */
export default {
  name: 'w-tabbar',
  props: {
    value: Number,
    iconClass: String
  },
  data () {
    return {
      currentIndex: -1,
      number: this.$children.length
    }
  },
  mounted () {
    if (this.value >= 0) {
      this.currentIndex = this.value
    }
    this.updateIndex()
  },
  methods: {
    // 为子元素生成索引,用于判断选中状态
    updateIndex () {
      if (!this.$children || !this.$children.length) {
        return false
      }
      this.number = this.$children.length
      let children = this.$children
      for (let i = 0; i < children.length; i++) {
        children[i].currentIndex = i
        if (children[i].currentSelected) {
          this.currentIndex = i
        }
      }
    }
  },
  watch: {
    currentIndex (val, oldVal) {
      // 清空原选中的子元素选中状态
      oldVal > -1 && this.$children[oldVal] && (this.$children[oldVal].currentSelected = false)
      // 设置当前选中的子元素选中状态
      val > -1 && this.$children[val] && (this.$children[val].currentSelected = true)
      this.$emit('input', val)
      this.$emit('on-index-change', val, oldVal)
    },
    value (val) {
      this.currentIndex = val
    }
  }
}
</script>

<style>

.wind-tabbar {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: fixed;
  z-index: 50;
  bottom: 0;
  width: 100%;
  background-color: #F7F7FA;
}
.wind-tabbar:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #C0BFC4;
  color: #C0BFC4;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}

</style>



