<template>
  <header class="wind-header">
	<div class="wind-header-left">
	  <slot name="overwrite-left">
		<transition :name="transition">
		  <a class="wind-header-back" @click.preventDefault v-show="_leftOptions.showBackBtn" @click="onClickBack">{{ typeof _leftOptions.leftBackText === 'undefined' ? '返回': _leftOptions.leftBackText}}</a>
		</transition>
		<transition :name="transition">
		  <div class="left-arrow" @click="onClickBack" v-show="_leftOptions.showBackBtn"></div>
		</transition>
	  </slot>
	  <slot name="header-left"></slot>
	</div>
	<h1 class="wind-header-title" @click="$emit('on-click-title')" v-if="!showOverWriteTitle">
		<slot>
			<transition :name="transition">
			  <span v-show="title">{{title}}</span>
			</transition>
		</slot>
	</h1>
	<div class="wind-header-title-area" v-if="showOverWriteTitle">
		<slot name="overwrite-title"></slot>
	</div>
	<div class="wind-header-right">
      <a class="wind-header-more" @click.preventDefault @click="$emit('on-click-more')" v-if="rightOptions.showMore"></a>
      <slot name="header-right"></slot>
    </div>
  </header>
</template>

<script type="text/babel">
/**
 * Header by shang 2017/12/30
 * @desc Header 顶部标题栏
 * 
 * @param {title}  [String] - 标题文本
 * @param {transitionName}  [String] - 切换路由页面动画效果名称
 * @param {leftOptions}  [Object] - 左侧设置
 * @param {leftOptions.showBackBtn}  [Boolean] - 显示左侧后退按钮
 * @param {leftOptions.leftBackText}  [String] - 左侧按钮文字
 * @param {leftOptions.preventGoBack}  [Boolean] - 左侧后退按钮是否阻止返回
 * @param {rightOptions}  [Object] - 右侧设置
 * @param {rightOptions.showMore}  [Boolean] - 是否显示右侧更多按钮
 * 
 * @event {on-click-back} 当阻止返回时 点击左侧后退按钮触发
 * @event {on-click-title} 点击标题触发
 * @event {on-click-more} 点击右侧更多按钮触发
 * 
 * @example
 *  <w-header :leftOptions="leftOptions" :rightOptions="rightOptions">
		这是标题显示不开会显示...
		<span slot="header-right">更多</span>
    </w-header>
	leftOptions: {
		showBackBtn: true,
		preventGoBack: false,
		leftBackText: '返回去'
	},
	rightOptions: {
		showMore: true
	}
 *
 */
export default {
	name: 'w-header',
	props: {
		title: String,
		transition: String,
		leftOptions: Object,
		rightOptions: {
	      type: Object,
	      default () {
	        return {
	          showMore: false
	        }
	      }
	    }
	},
	data () {
		return {
			showOverWriteTitle: false
		}
	},
	beforeMount () {
		if (this.$slots['overwrite-title']) {
			this.showOverWriteTitle = true
		}
	},
	computed: {
		_leftOptions () {
	      return Object.assign({
	        showBackBtn: true,
	        preventGoBack: false
	      }, this.leftOptions || {})
	    }
	},
	methods: {
		onClickBack () {
		  if (this._leftOptions.preventGoBack) {
			this.$emit('on-click-back')
		  } else {
			this.$router ? this.$router.back() : window.history.back()
		  }
		}
	}
}
</script>

<style>
@import '../../src/style/style.css';

.wind-header {
  position: absolute;
  top:0;
  left:0;
  padding: 3px 10px;
  box-sizing: border-box;
  background-color: var(--header-background-color);
  align-items: center;
  color: var(--header-color);
  display: flex;
  font-size: 14px;
  height: 46px;
  width: 100%;
  line-height: 46px;
  text-align: center;
  white-space: nowrap;
  z-index: 100;
}
.wind-header .wind-header-left ,.wind-header .wind-header-right{
	position: absolute;
    top: 14px;
    display: block;
    font-size: 14px;
    line-height: 21px;
    color:var(--header-color);
}
.wind-header .wind-header-left .wind-header-back {
	color:var(--header-color);
	padding-left: 16px
}
.wind-header .wind-header-left .left-arrow {
	position: absolute;
    width: 30px;
    height: 30px;
    top: -5px;
    left: -5px;
}
.wind-header .wind-header-left .left-arrow:before {
	content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border: 1px solid var(--header-color);
    border-width: 1px 0 0 1px;
    transform: rotate(315deg);
    top: 8px;
    left: 7px;
}
.wind-header .wind-header-title {
	line-height: 40px;
	text-align: center;
	font-size: 18px;
	font-weight: 400;
	color:var(--header-title-color);
}
.wind-header-title-area, .wind-header .wind-header-title {
  margin: 0 88px;
  height: 40px;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.wind-header .wind-header-title > span {
  display: inline-block;
}
.wind-header .wind-header-right {
  right: 15px
}
.wind-header .wind-header-right a,.wind-header .wind-header-right button {
  margin-left: 8px;
  margin-right: 0
}
.wind-header .wind-header-right .wind-header-more {
	color:var(--header-color);
}
.wind-header .wind-header-right .wind-header-more:after {
  content: "\2022\0020\2022\0020\2022\0020";
  font-size: 16px;
}
</style>



