import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Demos from '@/pages/Demos'
// import HelloWorld from '@/components/HelloWorld'
import ActionSheetDemo from '@/components/ActionSheetDemo'
import AlertDemo from '@/components/AlertDemo'
import ButtonDemo from '@/components/ButtonDemo'
import CellDemo from '@/components/CellDemo'
import CheckListDemo from '@/components/CheckListDemo'
import ConfirmDemo from '@/components/ConfirmDemo'
import DialogDemo from '@/components/DialogDemo'
import HeaderDemo from '@/components/HeaderDemo'
import IndexListDemo from '@/components/IndexListDemo'
import LoadingDemo from '@/components/LoadingDemo'
import SwipeDemo from '@/components/SwipeDemo'
import SwitchDemo from '@/components/SwitchDemo'
import PickerDemo from '@/components/PickerDemo'
import ToastDemo from '@/components/ToastDemo'
import TabbarDemo from '@/components/TabbarDemo'
import SimpleTabbarDemo from '@/components/SimpleTabbarDemo'
import PopupDemo from '@/components/PopupDemo'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
		  path: '/wind-ui',
		  name: 'Home',
		  component: Home,
		  meta: {
			title: '主界面'
		  }
		}, {
		  path: '/demos',
		  name: 'Demos',
		  component: Demos,
		  meta: {
			title: 'Demos'
		  }
		}, {
			path: '/actionSheetDemo',
			name: 'ActionSheetDemo',
			component: ActionSheetDemo,
			meta: {
				title: 'ActionSheetDemo'
			}
		}, {
			path: '/alertDemo',
			name: 'AlertDemo',
			component: AlertDemo,
			meta: {
				title: 'AlertDemo'
			}
		}, {
			path: '/buttonDemo',
			name: 'ButtonDemo',
			component: ButtonDemo,
			meta: {
				title: 'ButtonDemo'
			}
		}, {
			path: '/cellDemo',
			name: 'CellDemo',
			component: CellDemo,
			meta: {
				title: 'CellDemo'
			}
		}, {
			path: '/confirmDemo',
			name: 'ConfirmDemo',
			component: ConfirmDemo,
			meta: {
				title: 'ConfirmDemo'
			}
		}, {
			path: '/dialogDemo',
			name: 'DialogDemo',
			component: DialogDemo,
			meta: {
				title: 'DialogDemo'
			}
		}, {
			path: '/headerDemo',
			name: 'HeaderDemo',
			component: HeaderDemo,
			meta: {
				title: 'HeaderDemo'
			}
		}, {
			path: '/indexListDemo',
			name: 'IndexListDemo',
			component: IndexListDemo,
			meta: {
				title: 'IndexListDemo'
			}
		}, {
			path: '/loadingDemo',
			name: 'LoadingDemo',
			component: LoadingDemo,
			meta: {
				title: 'LoadingDemo'
			}
		}, {
			path: '/swipeDemo',
			name: 'SwipeDemo',
			component: SwipeDemo,
			meta: {
				title: 'SwipeDemo'
			}
		}, {
			path: '/SwitchDemo',
			name: 'SwitchDemo',
			component: SwitchDemo,
			meta: {
				title: 'SwitchDemo'
			}
		}, {
			path: '/pickerDemo',
			name: 'PickerDemo',
			component: PickerDemo,
			meta: {
				title: 'PickerDemo'
			}
		}, {
			path: '/toastDemo',
			name: 'ToastDemo',
			component: ToastDemo,
			meta: {
				title: 'ToastDemo'
			}
		}, {
			path: '/tabbarDemo',
			name: 'TabbarDemo',
			component: TabbarDemo,
			meta: {
				title: 'TabbarDemo'
			}
		}, {
			path: '/simpleTabbarDemo',
			name: 'SimpleTabbarDemo',
			component: SimpleTabbarDemo,
			meta: {
				title: 'SimpleTabbarDemo'
			}
		}, {
			path: '/popupDemo',
			name: 'PopupDemo',
			component: PopupDemo,
			meta: {
				title: 'PopupDemo'
			}
		}, {
			path: '/checkListDemo',
			name: 'checkListDemo',
			component: CheckListDemo,
			meta: {
				title: 'CheckListDemo'
			}
		}, {
	      path: '/',
	      redirect: '/wind-ui'
	    }, {
	      path: '*',
	      redirect: '/wind-ui'
	    }
	]
})



