<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				基本调用(menus为数组):{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				单一选项(menus为对象):{{value2?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2"></w-switch>
			</span>
		</w-cell>
		<w-actionSheet v-model="value" title="请选择一个选项" :menus="menus" :showCancel="true" v-on:on-cancel="onCancel" :clickMaskHide="true" v-on:on-menu-confirm="onConfirm" v-on:on-menu="onNoValue" @on-mask='onMask'></w-actionsheet>
		<w-actionSheet v-model="value2" title="请选择一个选项" :menus="menus2" :showCancel="true" v-on:on-menu-confirm="onConfirm" v-on:on-cancel="onCancel" :clickMaskHide="false" ></w-actionsheet>

	</div>
</template>

<script>

export default {
	name: 'actionSheetDemo',
	data () {
		return {
			value: false,
			value2: false,
			menus: [
				{
					label: '确定',
  					type: 'primary',
					value: 'confirm'
				},
				{
					label: 'warning',
  					type: 'warning',
					value: 'warning'
				},
				{
					label: 'disabled',
  					type: 'disabled',
					value: 'disabled'
				},
				{
					label: '<span style="color:blue;">Default+自定义</span>'
				},
				{
					label: '点我不自动关闭',
  					type: 'primary',
					clickMenuHide: false
				}
			],
			menus2: {
				label: '确定',
				type: 'primary',
				value: 'confirm'
			}
		}
	},
	created () {
	},
	methods: {
		onConfirm () {
			console.log('confirm回调')
		},
		onCancel () {
			console.log('cancel回调')
		},
		onMask () {
			console.log('onMask回调')
		},
		onNoValue () {
			console.log('不设置value的选项的回调')
		}
	}
}
</script>

<style>

</style>



