<template>
  <a href="javascript:;" class="wind-tabbar-item" @click="onItemClick"
    :class="{
      'wind-bar-item-on': isActive, 
      'wind-tabbar-simple': simple
    }"
  >
    <div class="wind-tabbar-icon" :class="[iconClass || $parent.iconClass, {'wind-reddot': showDot}]" v-if="!simple">
      <slot name="icon" v-if="!simple && !(hasActiveIcon && isActive)"></slot>
      <slot name="icon-active" v-if="!simple && hasActiveIcon && isActive"></slot>
    </div>
    <p class="wind-tabbar-label">
      <span :class="{'wind-reddot': showDot && simple}">
        <slot name="label"></slot>
      </span>
    </p>
  </a>
</template>

<script type="text/babel">
/**
 * Tabbar-item by shang 2018/1/6
 * @desc 底部tabbar-item 标签选项
 * @param {showDot} [boolean] - 待处理红点
 * @param {disabled}  [boolean] - 禁用
 * @param {iconClass}  [String] - 图标样式
 * @param {selected}  [boolean] - 选中
 *
 * @event {on-item-click} arg[currentIndex]  点击触发(发送当前标签索引)
 * 
 * @example
  <w-tabbar>
    <w-tabbar-item :selected='true' @on-item-click="itemClick">
      <img slot="icon" src="../assets/micOff.png">
      <img slot="icon-active" src="../assets/micOn.png">
      <span slot="label">News</span>
    </w-tabbar-item>
    <w-tabbar-item :showDot='true' @on-item-click="itemClick">
      <img slot="icon" src="../assets/micOff.png">
      <img slot="icon-active" src="../assets/micOn.png">
      <span slot="label">News</span>
    </w-tabbar-item>
  </w-tabbar>
 */
export default {
  name: 'w-tabbar-item',
  props: {
    // 待处理红点
    showDot: {
      type: Boolean,
      default: false
    },
    disabled:{
      type: Boolean,
      default: false
    },
    iconClass: String,
    selected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      simple: false,
      hasActiveIcon: false,
      currentIndex: -1,
      currentSelected: this.selected
    }
  },
  beforeMount () {
    // 判断是否开启simple模式
    if (!this.$slots.icon && !this.iconClass && !this.$parent.iconClass) {
      this.simple = true
    }
    // 判断开启选中图标模式
    if (this.$slots['icon-active']) {
      this.hasActiveIcon = true
    }
  },
  // mounted () {
  //   this.$parent.updateIndex()
  // },
  // beforeDestroy () {
  //   const $parent = this.$parent
  //   this.$nextTick(() => {
  //     $parent.updateIndex()
  //   })
  // },
  methods: {
    onItemClick () {
      if (typeof this.disabled === 'undefined' || this.disabled === false){
        this.currentSelected = true
        this.$parent.currentIndex = this.currentIndex
        this.$nextTick(() => {
            this.$emit('on-item-click', this.currentIndex)
        })
      }
    }
  },
  computed: {
    isActive () {
      return this.$parent.currentIndex === this.currentIndex
    }
  },
  watch: {
    currentSelected (val) {
      if (val) {
        this.$parent.currentIndex = this.currentIndex
      }
    }
  }
}
</script>

<style>
@import '../../src/style/style.css';

.wind-tabbar-item {
  display: block;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  padding: 5px 0 0;
  font-size: 0;
  color: #999999;
  text-align: center;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-decoration:none;
}
.wind-tabbar-item.wind-bar-item-on .wind-tabbar-icon,
.wind-tabbar-item.wind-bar-item-on .wind-tabbar-icon > i,
.wind-tabbar-item.wind-bar-item-on .wind-tabbar-label {
  color: #35495e;
}
.wind-tabbar-icon {
  display: inline-block;
  width: 27px;
  height: 27px;
}
i.wind-tabbar-icon,
.wind-tabbar-icon > i {
  font-size: 24px;
  color: #999999;
}
.wind-tabbar-icon img {
  width: 100%;
  height: 100%;
}
.wind-tabbar-label {
  text-align: center;
  color: #999999;
  font-size: 10px;
  line-height: 1.8;
  margin:0;
  padding:0;
}
.wind-tabbar-icon {
  position: relative;
}
/*.wind-tabbar-icon > sup {
  position: absolute;
  top: -8px;
  left: 100%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 101;
}*/
.wind-tabbar-item.wind-tabbar-simple {
  padding: 0;
  height: 50px;
  line-height: 50px;
}
.wind-tabbar-simple .wind-tabbar-label {
  font-size: 14px;
  line-height: 50px;
}

</style>



