import Alert from './Alert.vue'

export default {
  /**
   * 每个插件都有的install方法，用于安装插件
   * @param {Object} Vue - Vue类
   * @param {Object} [pluginOptions] - 插件安装配置
   */
  install(Vue, pluginOptions = {}) {
    // 创建"子类"方便挂载
    const VueAlert = Vue.extend(Alert)
    let alert = null
    let alertCallbackObj = {}
    const defaultCallback = action => {
      if (alertCallbackObj.resolve) {
        if (action === 'confirm') {
          alertCallbackObj.resolve(action)
        } 
      }
    }

    /**
     * 初始化并显示confirm
     * @returns {Promise} Promise实例
     */
    function $alert(options = {}) {
      return new Promise((resolve, reject) => {
        // 第一次调用
        if (!alert) {
          alert = new VueAlert()
          // 手动创建一个未挂载的实例
          alert.$mount()
        }
        // if (alert.isShow) {
        //   return
        // }
        alert.title = options.title || '提示'
        alert.message = typeof options === 'string' ? options : options.message || ''
        alert.btnConfirmText = options.btnConfirmText || '确定'
        alert.titleColor = options.titleColor || '#000'
        alert.messageColor = options.messageColor || '#999'
        alert.clickMaskHide = options.clickMaskHide || false
        alert.callback = defaultCallback
        if (typeof Promise !== 'undefined') {
          alertCallbackObj.resolve = resolve
          alertCallbackObj.reject = reject
        }
        // 挂载.
        document.querySelector(pluginOptions.container || 'body').appendChild(alert.$el)
        // 显示confirm
        alert.show()
        // resolve()
      })
    }
    // 定义关闭confirm方法
    $alert.close = () => {
      return new Promise((resolve) => {
        if (!alert || !alert.isShow) {
          resolve()
          return
        }
        alertCallbackObj = {}
        alert.hide()
        resolve()
      })
    }

    Vue.$WAlert = Vue.prototype.$WAlert = $alert
  }
}


