<template>
  <transition name="wind-toast-transition">
    <div class="wind-toast" v-show="visible">
      <div class="wind-toast-mask" v-show = "maskShow && visible" @touchmove.prevent></div>
      <transition :name="currentTransition">
        <div class="wind-toast-content" v-show="visible" :class="customClass" 
          :style="{ 
            'padding': iconClass === '' ? '10px' : '20px',
            'width': typeof width === 'number'? `${width}px` : width
          }">
          <i class="wind-toast-icon" :class="currentIconClass" v-if="type !== 'text'"></i>
          <span class="wind-toast-text" :class="{'warning-font':type === 'warning'}"
           :style="{
            'padding-top': iconClass === '' ? '0' : '10px',
            'white-space': type !== 'text' ? 'nowrap' : 'normal'
         }">{{ message }}</span>
        </div>
      </transition>
    </div>
  </transition>
</template>


<script type="text/babel">
/**
 * Toast by shang 2017/11/21
 * @desc Toast提示
 * @param {message} [str] - 显示在下方的文本信息
 * @param {duration}  [nub] - 提示框显示时间
 * @param {iconClass}  [str] - 提示框自定义图标
 * @param {position}  [str] - 提示框位置 可选[top,bottom,middle]
 * @param {type}  [str] - 提示框内置样式对应的类型 可选[success,warning,cancel,text]
 * @param {width}  [String,Number] - 提示框宽度(如果用str需要加上px,或百分号)
 * @param {maskShow}  [str] - 是否显示遮罩层
 *
 * @event {on-show} 显示时触发
 * @event {on-hide} 隐藏时触发
 *
 * @plugin 开启 vue.$WToast({}).then()
 * 
 * @then(arg) 
 * arg 关闭Toast组件的回调
 * 
 * @example
 * <w-toast v-model="show"></w-toast>
 * 提示框消失 自动 $emit  value:false, 绑定v-model 即可双向绑定
 * 或
 * this.$WToast({
 *   message: 'middle提示',
 *   position: 'middle',
 *   type: 'warning',
 * })
 */
  export default {
    props: {
      message: String,
      transition: String,
      value: Boolean,
      duration: {
        type: Number,
        default: 3000
      },
      className: {
        type: String,
        default: ''
      },
      position: {
        type: String,
        default: 'default'
      },
      iconClass: {
        type: String,
        default: ''
      },
      type: {
        type: String,
        default: 'success'
      },
      maskShow: {
        type: Boolean,
        default: false
      },
      width: {
        type: [String,Number],
        default: 120
      }
    },
    data() {
      return {
        visible: false,
        show: false
      };
    },
    created () {
      if (this.value) {
        this.show = true
      }
    },
    computed: {
      currentTransition () {
        if (this.transition) {
          return this.transition
        }
        if (this.position === 'top') {
          return 'mint-toast-pop-top'
        }
        if (this.position === 'bottom') {
          return 'mint-toast-pop-bottom'
        }
        if (this.position === 'middle') {
          return 'mint-toast-pop-middle'
        }
        return 'mint-toast-pop'
      },
      customClass() {
        var classes = [];
        switch (this.position) {
          case 'top':
            classes.push('is-placetop');
            break;
          case 'bottom':
            classes.push('is-placebottom');
            break;
          case 'middle':
            classes.push('is-placemiddle');
            break;
          default:
            classes.push('is-placedefault');
        }
        classes.push(this.className);

        return classes.join(' ');
      },
      currentIconClass () {
        if (this.iconClass !== '') {
          return this.iconClass
        }
        if (this.type === 'success') {
          return 'icon-success'
        }
        if (this.type === 'warning') {
          return 'icon-warning warning-font'
        }
        if (this.type === 'cancel') {
          return 'icon-cancel'
        }
      }
    },
    watch: {
      show (val) {
        if (val) {
          this.visible = true
          this.$emit('input', true)
          this.$emit('on-show',true)

          clearTimeout(this.timeout)
          this.timeout = setTimeout(() => {
            this.visible = false
            this.$emit('input', false)
            this.$emit('on-hide',false)
          }, this.duration)
        }
      },
      value (val) {
        this.show = val
      }
    }
  };
</script>

<style>
  @import '../../src/style/style.css';
  .wind-toast {
      position: fixed;
      max-width: 80%;
      border-radius: 5px;
      background: rgba(0, 0, 0, 0.7);
      color: #fff;
      box-sizing: border-box;
      text-align: center;
      z-index: 1000;
      transition: opacity .3s linear;
  }
  .wind-toast-mask {
      position: fixed;
      z-index: 1000;
      background-color: rgba(0,0,0,0.4);
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
  }
  .wind-toast-content {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    box-sizing: border-box;
    text-align: center;
    z-index: 5000;
    /*transition: opacity .3s linear;*/
    transition: all 400ms cubic-bezier(.36,.66,.04,1);
  }
  .wind-toast-icon {
    display: block;
    text-align: center;
    font-size: 56px;
    /*width:100px;*/
    width:100%;
    padding:15px 0;
    margin:auto;
  }
  
  .wind-toast-text {
    font-size: 14px;
    display: block;
    text-align: center;
    overflow: hidden;
  }
  .wind-toast-text.warning-font {
    color:var(--toast-warning-font);
  }
  .icon-warning.warning-font {
    color:var(--toast-warning-font);
  }
  .is-placetop {
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
  }
  
  .is-placemiddle {
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  
  .is-placebottom {
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
  }

  .is-placedefault {
    top:150px;
    left: 50%;
    transform: translateX(-50%);
  }
  
  /*toast渐进渐出*/
 .wind-toast-transition-enter,.wind-toast-transition-leave-active {
    opacity: 0;
  }
  .wind-toast-transition-leave {
    opacity: 1;
  }

  /*默认,middle*/
  .mint-toast-pop-enter, .mint-toast-pop-middle-enter,
  .mint-toast-pop-leave-active,.mint-toast-pop-middle-leave-active {
    opacity: 0;
  }
  /*top*/
  .mint-toast-pop-top-enter, .mint-toast-pop-top-leave-active {
    opacity: 0;
    top: 0;
  }
  
  .mint-toast-pop-top-leave {
    opacity: 1;
    top:50px;
  }
  /*bottom*/
  .mint-toast-pop-bottom-enter, .mint-toast-pop-bottom-leave-active {
    opacity: 0;
    bottom:0;
  }
  .mint-toast-pop-bottom-leave {
    opacity: 1;
    bottom:50px;
  }
  
  
</style>



