<template>
	<div class="wind-actionsheet-mask" @click.self="onMaskClick" v-show="tipsMaks">
		<div class="wind-actionsheet-wrapper" :class="{'wind-actionsheet-start':topMove,'wind-actionsheet-end':!topMove}">
			<div class="wind-actionsheet-title" v-if="!!title"><p>{{title}}</p></div>
			<div class="wind-actionsheet-btns" >
				<div class="wind-actionsheet-button" :class="`wind-actionsheet-button-${menus.type || 'default'}`" @click="onMenuClick(menus)"  v-html="menus.label" v-if="{}.toString.call(menus) === '[object Object]'"></div>
				<div  v-for="(menu, key) in menus" class="wind-actionsheet-button" :class="`wind-actionsheet-button-${menu.type || 'default'}`" @click="onMenuClick(menu, key)" v-html="menu.label" v-else></div>
				<div class="wind-actionsheet-cancelBtn" @click="onCancel"  v-if="showCancel">{{cancelText}}</div>
			</div>
		</div>
	</div>
</template>
<script type="text/babel">
/**
 * ActionSheet by shang 2017/12/17
 * @desc actionSheet 提示框
 * @param {value} [boolean] - 显示/关闭  支持双向绑定
 * @param {title} [String] - 提示框标题
 * @param {menus} [Object, Array] - 菜单
 * 
 * @menus {label} [String] - 菜单文字
 * @menus {type} [String] - 菜单类型(primary,warning,disabled,自定义)
 * @menus {value} [String] - 用于触发 on-menu-${value}
 * @menus {clickMenuHide} [Boolean] - 点击选项是否关闭actionsheet
 * 
 * @param {showCancel} [Boolean] - 显示cancel按钮
 * @param {cancelText} [String] - cancel按钮内容
 * @param {clickMaskHide} [Boolean] - 点击遮罩关闭
 * 
 * @event {on-menu} 点击菜单触发(menu没有value)
 * @event {on-menu-${menuvalu}} 点击菜单触发(menu有value)
 * @event {on-mask} 点击遮罩触发
 * @event {on-cancel} 点击取消按鈕触发
 *
 * @example
 * <w-actionSheet v-model="switchValue14" title="这是一个提示" :menus="menus" v-on:on-cancel="onCancel"></w-actionsheet>
 * menus: [
		{
			label: '确定',
			type: 'primary',
			value: 'confirm'
			clickMenuHide: false
		},
		{
			label: 'warning',
			type: 'warning',
			value: 'warning'
		},
		{
			label: 'disabled',
			type: 'disabled',
			value: 'disabled'
		},
		{
			label: '<span style="color:blue;">Default+自定义</span>'
		}
	]
 */

export default {
	name: 'w-actionSheet',
	props: {
		value:Boolean,
		title: {
			type: String,
			default: ''
		},
		menus: {
		  type: [Object, Array],
		  default: () => ({
			label: '确定',
			type: 'primary',
			value: 'confirm',
			clickMenuHide: true
		  })
		},
		showCancel: {
			type: Boolean,
			default: true
		},
		cancelText:{
			type: String,
			default: '取消'
		},
		clickMaskHide: {
			type: Boolean,
			default: true
		}
	},
	data () {
		return {
			topMove: false,
			tipsMaks: false
		}
	},
	methods: {
		onMenuClick (menu, key) {
			if (menu.type !== 'disabled') {
				if (menu.value) {
					this.$emit(`on-menu-${menu.value}`)
				} else {
					this.$emit('on-menu')
				}
				if (menu.clickMenuHide !== false) {
					this.$emit('input', false)
					this.topMove = false
					setTimeout(() => {
						this.tipsMaks = false
					}, 100)
				}
			}
		},
		onMaskClick () {
			if (this.clickMaskHide) {
				this.topMove = false
				this.$emit('input', false)
				this.$emit('on-mask')
				setTimeout(() => {
					this.tipsMaks = false
				}, 250)
			}
		},
		onCancel () {
			this.topMove = false
			this.$emit('input', false)
			this.$emit('on-cancel')
			setTimeout(() => {
				this.tipsMaks = false
			}, 250)
		},
		open () {
			this.topMove = true
			this.tipsMaks = true
		}
	},
	computed: {
		upMove () {
			if (this.tipsState) {
				this.topMove = true
			} else {
				this.topMove = false
			}
			return this.topMove
		}
	},
	watch: {
		value(val) {
			if (val) {
				this.open()
			}
		}
	}
}
</script>

<style>
	.wind-actionsheet-mask{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 100;
		background-color: rgba(0,0,0,.4);
		/*opacity: 0;*/
	}
	.wind-actionsheet-mask .wind-actionsheet-wrapper{
		position: absolute;
		width: 100%;
		background-color: #eee;
		box-shadow: 0px 5px 10px rgba(0,0,0,.1);
	}
	.wind-actionsheet-mask .wind-actionsheet-wrapper .wind-actionsheet-title{
		margin: 0;
		text-align: center;
		/*border-bottom: solid 1px #f3f3f3;*/
		box-sizing: border-box;
		vertical-align: middle;
		background-color: #fff;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-title p{
		height: auto;
		vertical-align: middle;
		font-size: .9em;
		margin: 0;
		padding: 10px;
		color: #aaa;
	}
	.wind-actionsheet-mask .wind-actionsheet-wrapper .wind-actionsheet-btns{
		margin: 0;
		padding: 0;
		text-align: center;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-button{
		display: block;
		font-size: 1.2em;
		width: 100%;
		padding: .6em 0;
		box-sizing: border-box;
		background-color: #fff;
		border-top: solid 1px #f3f3f3;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-button-default {
		color:#000;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-button-primary {
		color:#26a2ff;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-button-warning {
		color:#E64340;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-button-disabled {
		color:#ccc;
	}
	.wind-actionsheet-wrapper .wind-actionsheet-btns .wind-actionsheet-cancelBtn{
		display: block;
		font-size: 1.2em;
		width: 100%;
		padding: .6em 0;
		box-sizing: border-box;
		background-color: #fff;
		margin-top: .5em;
		color:#000;
	}
	.wind-actionsheet-start{
		bottom: 0%;
		animation: tipsStart .3s;
		-webkit-animation: tipsStart .3s;
	}
	@keyframes tipsStart{
		from {transform: translateY(100%);}
		to {transform: translateY(0);}
	}
	@-webkit-keyframes tipsStart{
			from {transform: translateY(100%);}
			to {transform: translateY(0);}
	}
	.wind-actionsheet-end{
		bottom: 0%;
		animation: tipsEnd .3s;
		-webkit-animation: tipsEnd .3s;
	}
	@keyframes tipsEnd{
			from {transform: translateY(0);}
			to {transform: translateY(100%);}
	}
	@-webkit-keyframes tipsEnd{
			from {transform: translateY(0);}
			to {transform: translateY(100%);}
	}
</style>


