import WSwitch from './packages/switch' //switch 开关
import WLoading from './packages/loading/Loading.vue' //loading 加载
import WLoadingType from './packages/loadingType' //loadingType 加载动画
import WToast from './packages/toast/Toast.vue' //toast 提示
import WDialog from './packages/w-dialog' //dialog 展示框
import WConfirm from './packages/confirm/Confirm.vue' //confirm 提示框
import WAlert from './packages/alert/Alert.vue' //alert 提示框
import WActionSheet from './packages/actionsheet' //actionSheet 提示框
import WPopup from './packages/popup' //popup 弹出框
import WButton from './packages/button' //button 按钮
import WHeader from './packages/header' //button 按钮
import WCell from './packages/cell' //cell 单元格
import WPicker from './packages/picker' //picker 滚动选择框
import WCheckList from './packages/checkList' //checkList 勾选单元格
import {Tabbar, TabbarItem} from './packages/tabbar' //tabbar 底部分页标签
import {IndexList, IndexListItem} from './packages/indexList' //tabbar 底部分页标签
import {Swipe, SwipeItem} from './packages/swipe' //swipe 轮播图

import WLoadingPlugin from './packages/loading' //loading 加载(plugin)
import WToastPlugin from './packages/toast' //toast 提示(plugin)
import WConfirmPlugin from './packages/confirm' //confirm 提示框(plugin)
import WAlertPlugin from './packages/alert' //alert 提示框(plugin)

import './src/style/style.css'

const WindUI = {
	install: function (Vue) {
                Vue.component('w-switch', WSwitch)
                Vue.component('w-loading', WLoading)
                Vue.component('w-loadingType', WLoadingType)
                Vue.component('w-toast', WToast)
                Vue.component('w-dialog', WDialog)
                Vue.component('w-confirm', WConfirm)
                Vue.component('w-alert', WAlert)
                Vue.component('w-actionSheet', WActionSheet)
                Vue.component('w-popup', WPopup)
                Vue.component('w-button', WButton)
                Vue.component('w-tabbar', Tabbar)
                Vue.component('w-tabbar-item', TabbarItem)
                Vue.component('w-header', WHeader)
		Vue.component('w-cell', WCell)
                Vue.component('w-indexList', IndexList)
                Vue.component('w-indexList-item', IndexListItem)
                Vue.component('w-swipe', Swipe)
                Vue.component('w-swipe-item', SwipeItem)
                Vue.component('w-picker', WPicker)
                Vue.component('w-checkList', WCheckList)
        
                // 可以plugin模式调用的UI
                Vue.use(WLoadingPlugin) 
                Vue.use(WToastPlugin)
                Vue.use(WConfirmPlugin)
                Vue.use(WAlertPlugin)
	}
}

export default WindUI



