<template>
	<label class="wind-switch">
		<input class="wind-switch-input" :disabled="disabled" @change="$emit('change', !currentValue)" type="checkbox" v-model="currentValue">
		<span class="wind-switch-core" :style="{
			opacity:disabledStyle
		}"></span>
		<div class="wind-switch-label"><slot></slot></div>
	</label>
</template>
<script>
/**
 * w-switch by shang 2017/11/1
 * @desc 切换按钮
 * @param {value} [boolean] - 绑定值，支持双向绑定
 * @param {slot}  [str] - 显示内容
 * @param {disabled} [slot] - 是否禁用
 *
 * @example
 * <w-switch v-model="value"></w-switch>
 * 1.一般使用绑定value即可实现双向绑定
 * 2.如有权限需求 绑定 disabled 即可,禁用状态下 按钮会有0.6的透明度
 * 3.需要根据开启/关闭 执行函数 绑定change 即可 v-on:change
 */
export default {
	name: 'w-switch',
	props: {
		value: Boolean,
		disabled: {
			type: Boolean,
			default: false
		}
	},
	// 实时计算
	computed: {
		currentValue: {
			get () {
				return this.value
			},
			set (val) {
				this.$emit('input', val)
			}
		},
		disabledStyle() {
			return this.disabled ? 0.6 : 1
		}
	}
}
</script>

<style lang="css">
.wind-switch {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	position: relative;
}
.wind-switch * {
	pointer-events: none;
}
.wind-switch-label {
	margin-left: 10px;
	display: inline-block;
}
.wind-switch-label:empty {
	margin-left: 0;
}
.wind-switch-core {
	display: inline-block;
	position: relative;
	width: 52px;
	height: 32px;
	border: 1px solid #d9d9d9;
	border-radius: 16px;
	box-sizing: border-box;
	background: #d9d9d9;
}
.wind-switch-core::after, .wind-switch-core::before {
	content: " ";
	top: 0;
	left: 0;
	position: absolute;
	-webkit-transition: -webkit-transform .3s;
	transition: -webkit-transform .3s;
	transition: transform .3s;
	transition: transform .3s, -webkit-transform .3s;
	border-radius: 15px;
}
.wind-switch-core::after {
	width: 30px;
	height: 30px;
	background-color: #fff;
	box-shadow: 0 1px 3px rgba(0, 0, 0, .4);
}
.wind-switch-core::before {
	width: 50px;
	height: 30px;
	background-color: #fdfdfd;
}
.wind-switch-input {
	display: none;
}
.wind-switch-input:checked + .wind-switch-core {
	border-color: #26a2ff;
	background-color: #26a2ff;
}
.wind-switch-input:checked + .wind-switch-core::before {
	-webkit-transform: scale(0);
			transform: scale(0);
}
.wind-switch-input:checked + .wind-switch-core::after {
	-webkit-transform: translateX(20px);
			transform: translateX(20px);
}

</style>



