<template>
	<div id="app">
		<!-- head -->
		<w-header :transition="transitionName" :leftOptions="leftOptions" :rightOptions="rightOptions" :title="headerTitle">
		</w-header>
		<!-- 内容 -->
		<transition :name="transitionName" mode="out-in">
			<router-view class="page-content" keep-alive></router-view>
		</transition>
		<!-- 底部标签 -->
		<w-tabbar :value="selectTab" v-show="homeTabbarShow">
			<w-tabbar-item  @on-item-click="itemClick">
				<img slot="icon" src="./assets/home.png">
				<img slot="icon-active" src="./assets/home-active.png">
				<span slot="label">Home</span>
			</w-tabbar-item>
			<w-tabbar-item :showDot='true' @on-item-click="itemClick">
				<img slot="icon" src="./assets/demo.png">
				<img slot="icon-active" src="./assets/demo-active.png">
				<span slot="label">Demos</span>
			</w-tabbar-item>
		</w-tabbar>
	</div>
</template>

<script>
// 存放path数组
const pathHistory = []

export default {
	name: 'app',
	data () {
		return {
			// 切换路由页面动画效果名称
			transitionName: 'page-next',
			// header options
			headerTitle: 'home',
			leftOptions: {
				showBackBtn: true,
	        	preventGoBack: false,
	        	leftBackText: 'goback'
			},
			rightOptions: {
				showMore: false
			},
			selectTab: 0,
			homeTabbarShow: true
		}
	},
	created () {
	},
	methods: {
		itemClick (index) {
			if (index === 0) {
				this.headerTitle = '首页'
				this.$router.replace('/wind-ui')
			} else if (index === 1) {
				this.headerTitle = 'demo'
				this.$router.replace('/demos')
			}
		}
	},
	// 监听
	watch: {
		/*
			监听路由变化:
			1.每次页面向后跳转,把前一个页面的路由存入path数组
			2.判断-如果跳转目的页面路由,是path数组末尾,证明此行为为返回上一个页面
			  则 删除 path数组末尾 即 目的页面路由
			3.根据路由情况改变页面切换动画效果
		 */
		'$route' (to, from) {
			// path数组末尾
			const len = pathHistory.length - 1
			// 判断 非起始页面 且 目的路由 是否已经存在path数组中
			if ((len >= 0 && pathHistory[len] === to.path) || to.path === '/wind-ui') {
				// 删除path数组 末尾 即 目的页面路由
				pathHistory.pop()
				// this.transitionName = 'page-prev'
				this.transitionName = 'page-fade-in-left'
			} else {
				// 向path数组末尾添加 目的页面路由
				pathHistory.push(from.path)
				// this.transitionName = 'page-next'
				this.transitionName = 'page-fade-in-right'
			}
		}
	}
}
</script>

<style>
/*.view {
	-webkit-transition: all 300ms ease;
	transition: all 300ms ease;
}
.page-prev-enter {
	-webkit-transform: translate(-100%, 0);
	transform: translate(-100%, 0);
}
.page-next-enter {
	-webkit-transform: translate(100%, 0);
	transform: translate(100%, 0);
}
.page-prev-leave-active, .page-next-leave-active {
	opacity: 0;
	transition: none;
}*/

/*页面从右向左进入*/
.page-fade-in-right-enter-active {
  animation: fadeinR .5s;
}
/*页面从左向右进入*/
.page-fade-in-left-enter-active {
  animation: fadeinL .5s;
}
@keyframes fadeinR {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity:1;
    transform:translateX(0);
  }
}
@keyframes fadeinL{
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
/*防止出现变量名*/
[v-cloak] {
	display: none;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	height: 100%;
}
.page-content {
	overflow-y: scroll;
	/*position: absolute;*/
	/*top:0;*/
	/*bottom: 53px;*/
	padding-top:46px;
	padding-bottom:53px;
	height: 100%;
}
</style>



