<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				基本调用:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				全屏调用:{{value1?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value1"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				自定义样式:{{value2?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2" @change="cutomPopupShow"></w-switch>
			</span>
		</w-cell>
		<w-popup v-model="value" :clickMaskHide="false" position='bottom' :show-mask='true' height="30%" @on-show="onShow" @on-hide='onHide' @on-mask='onMask'>
			<w-cell title="关闭">
				<span slot="cell-value">
					<w-switch v-model="value"></w-switch>
				</span>
			</w-cell>
			<w-cell title="提示">
				<span slot="cell-value">
					<w-switch v-model="toastValue" @change="showToast"></w-switch>
				</span>
			</w-cell>
		</w-popup>
		<w-popup v-model="value1" :clickMaskHide="false" position='right' :show-mask='false' width="100%">
			<w-cell title="关闭">
				<span slot="cell-value">
					<w-switch v-model="value1"></w-switch>
				</span>
			</w-cell>
		</w-popup>
		<w-popup v-model="value2" :clickMaskHide="false" position='top' :show-mask='false' :popupStyle="popupStyle">更新成功</w-popup>
	</div>
</template>

<script>

export default {
	name: 'dialogDemo',
	data () {
		return {
			value: false,
			value1: false,
			value2: false,
			toastValue: false,
			popupStyle: {
				height: '46px',
				lineHeight: '46px',
				background: 'black',
				opacity: '0.8',
				fontSize: '14px',
				color: 'white'
			}
		}
	},
	created () {
	},
	methods: {
		onMask () {
			console.log('mask')
		},
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		},
		showToast () {
			this.$WToast({
				message: '成功',
				duration: 1000
			}).then(() => {
				this.toastValue = false
			})
		},
		cutomPopupShow (val) {
			if (val) {
				if (this.popupTimer) {
					clearTimeout(this.popupTimer)
				}
				this.popupTimer = setTimeout(() => {
					this.value2 = false
				}, 2000)
			}
		}
	}
}
</script>

<style>

</style>



