<template>
    <div class="wind-flower-wrapper" :style="{
    'height': loadingTypeSize,
    'width': loadingTypeSize
  }">
        <div class="wind-flower-loader">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
    </div>
</template>
<script type="text/babel">
  import common from './common.vue'

  export default {
    name: 'flower',

    mixins: [common]
    
  }
</script>

<style>
    .wind-peas-wrapper {
      border-radius: 5px;
    }
    .wind-flower-loader{
        height:50px;
        width:50px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .wind-flower-loader span{
        display: inline-block;
        width: 10px;
        height: 3px;
        border-top-left-radius: 2px;
        border-bottom-left-radius: 2px;
        background: rgb(255,255,255);
        position: absolute;
        opacity: 0.2;
        -webkit-animation: load 1s ease infinite;
    }
    @-webkit-keyframes load{
        0%{
            opacity: 1;
        }
        100%{
            opacity: 0.2;
        }
    }
    .wind-flower-loader span:nth-child(1){
        left: 5px;
        top: 50%;
        /*margin-top:-5px;*/
        transform: translateY(-50%);
        -webkit-animation-delay:0.13s;
    }
    .wind-flower-loader span:nth-child(2){
        left: 10px;
        top: 15px;
        -webkit-transform: rotate(45deg);
        -webkit-animation-delay:0.26s;
    }
    .wind-flower-loader span:nth-child(3){
        left: 50%;
        top: 8px;
        /*margin-left: -15px;*/
        -webkit-transform: translate(-50%,0) rotate(90deg); 
        -webkit-animation-delay:0.39s;
    }
    .wind-flower-loader span:nth-child(4){
        top: 15px;
        right:10px;
        -webkit-transform: rotate(135deg);
        -webkit-animation-delay:0.52s;
    }
    .wind-flower-loader span:nth-child(5){
        right: 5px;
        top: 50%;
        /*margin-top:-5px;*/
        -webkit-transform: translate(0,-50%) rotate(180deg) ;
        -webkit-animation-delay:0.65s;
    }
    .wind-flower-loader span:nth-child(6){
        right: 10px;
        bottom:15px;
        -webkit-transform: rotate(225deg);
        -webkit-animation-delay:0.78s;
    }
    .wind-flower-loader span:nth-child(7){
        bottom: 8px;
        left: 50%;
        /*margin-left: -15px;*/
        -webkit-transform: translate(-50%,0) rotate(270deg); 
        -webkit-animation-delay:0.91s;
    }
    .wind-flower-loader span:nth-child(8){
        bottom: 15px;
        left: 10px;
        -webkit-transform: rotate(315deg);
        -webkit-animation-delay:1.04s;
    }


</style>


