<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				显示:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value" @change="showLoading"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件调用,3秒后关闭,loadtype:菊花
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2" @change='showLoading2'></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件调用,3秒后关闭,loadtype:圆点
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3" @change='showLoading3'></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件调用,3秒后关闭,只传递一个text参数
			</span>
			<span slot="cell-value">
				<w-switch v-model="value4" @change='showLoading4'></w-switch>
			</span>
		</w-cell>
		<w-loading v-model="value" :text="message" @on-show="onShow" @on-hide="onHide"></w-loading>
	</div>
</template>

<script>

export default {
	name: 'alertDemo',
	data () {
		return {
			message: '0%',
			value: false,
			value2: false,
			value3: false,
			value4: false
		}
	},
	created () {
	},
	methods: {
		showLoading (val) {
			if (val) {
		        this.tick(0, (percent) => {
		          if (percent === 100) {
		            this.value = false
		            return
		          }
		          this.message = `${percent}%`
		        })
		    }
		},
		tick (i, cb) {
			let _this = this
			setTimeout(function () {
		    	i++
			    cb(i)
			    if (i < 100) {
			      _this.tick(i, cb)
			    }
		  	}, 25)
		},
		showLoading2 () {
			if (!this.value2) {
				return false
			}
			if (this.loadingTimer1) {
				clearTimeout(this.loadingTimer1)
			}
			this.$WLoading({
				loadingType: 'flower'
			}).then(() => {
				console.log('开启Loading')
			})
			this.loadingTimer1 = setTimeout(() => {
				this.$WLoading.end().then(() => {
					console.log('关闭Loading')
				})
				this.value2 = false
			}, 3000)
		},
		showLoading3 () {
			if (!this.value3) {
				return false
			}
			if (this.loadingTimer2) {
				clearTimeout(this.loadingTimer2)
			}
			this.$WLoading({
				loadingType: 'peas'
			}).then(() => {
				console.log('开启Loading')
			})
			this.loadingTimer2 = setTimeout(() => {
				this.$WLoading.end().then(() => {
					console.log('关闭Loading')
				})
				this.value3 = false
			}, 3000)
		},
		showLoading4 () {
			if (!this.value4) {
				return false
			}
			if (this.loadingTimer3) {
				clearTimeout(this.loadingTimer3)
			}
			this.$WLoading('加载中...').then(() => {
				console.log('开启Loading')
			})
			this.loadingTimer3 = setTimeout(() => {
				this.$WLoading.end().then(() => {
					console.log('关闭Loading')
				})
				this.value4 = false
			}, 3000)
		},
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		}
	}
}
</script>

<style>

</style>



