<template>
	<div>
		<w-cell>
			<span slot="cell-title">
				基本调用:{{value?'开启':'关闭'}}
			</span>
			<span slot="cell-value">
				<w-switch v-model="value"></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件方式调用
			</span>
			<span slot="cell-value">
				<w-switch v-model="value2" @change='showConfirm2'></w-switch>
			</span>
		</w-cell>
		<w-cell>
			<span slot="cell-title">
				插件方式调用,3秒后关闭
			</span>
			<span slot="cell-value">
				<w-switch v-model="value3" @change='showConfirm3'></w-switch>
			</span>
		</w-cell>
		<w-confirm v-model="value" :title="title" :message="message" :clickMaskHide="true" v-on:on-confirm="onConfirm" v-on:on-cancel="onCancel" @on-mask="onMask" @on-show='onShow' @on-hide='onHide'></w-confirm>
	</div>
</template>

<script>

export default {
	name: 'confirmDemo',
	data () {
		return {
			title: '提示',
			message: '是否发送消息',
			value: false,
			value2: false,
			value3: false
		}
	},
	created () {
	},
	methods: {
		showConfirm2 () {
			if (!this.value2) {
				return false
			}
			this.$WConfirm({
				title: '调查',
				message: '你会选择vue框架吗',
				clickMaskHide: true,
				btnConfirmText: '会使用',
				btnCancelText: '我选择react',
				titleColor: 'red',
				messageColor: 'green'
			}).then(() => {
				console.log('confirm的回调')
				this.value2 = !this.value2
			}, () => {
				console.log('cancel的回调')
				this.value2 = !this.value2
			})
		},
		showConfirm3 () {
			if (!this.value3) {
				return false
			}
			if (this.confirmTimer) {
				clearTimeout(this.confirmTimer)
			}
			this.$WConfirm({
				message: '3秒后自动关闭'
			}).then(() => {
				console.log('confirm的回调')
				this.value3 = false
			}, () => {
				console.log('cancel的回调')
				this.value3 = false
			})
			this.confirmTimer = setTimeout(() => {
				this.$WConfirm.close()
				this.value3 = false
			}, 3000)
		},
		onConfirm () {
			console.log('confirm')
		},
		onCancel () {
			console.log('cancel')
		},
		onMask () {
			console.log('mask')
		},
		onShow () {
			console.log('show')
		},
		onHide () {
			console.log('hide')
		}
	}
}
</script>

<style>

</style>



